import React from 'react';
import { connect } from "react-redux";
import { Link } from 'react-router-dom';
import { updateCurrentNav, calculateSubtotal, submitOrder, getDeliveryMethod, handleTextarea } from '../../redux/actions/main';
import { Row, Col, Card, ListGroup, ButtonToolbar, Button, Spinner } from 'react-bootstrap';

import TopNav from '../common/TopNav.js';
// import Bottom from '../bottom';

import BagelShmear from './BagelShmear';
import EggSandwich from './EggSandwich';
import BreakfastFavorites from './BreakfastFavorites';
import SweetSnack from './SweetSnack';
import Beverage from './Beverage';
import FreshSalads from './FreshSalads';
import Cookie from './Cookie';
import LunchSandwiches from './LunchSandwiches';

import './style.css';

const roundTo = require('roundto');

class Summary extends React.Component {

    constructor(props) {
        super(props);
        this.props.updateCurrentNav(8);
        
        const subtotal = roundTo(
            this.props.globalState.bagelandsheamr.total +
            this.props.globalState.egg_sandwich.total + 
            this.props.globalState.bk_fav_reducer.total +
            this.props.globalState.sweet_snack.total + 
            this.props.globalState.beverage.total + 
            this.props.globalState.fresh_salads.total + 
            this.props.globalState.cookie.total +
            this.props.globalState.lunchsandwich.total
            , 2);
        const tax = roundTo(subtotal*0.061, 2);
        const total = roundTo(subtotal + tax, 2);

        this.props.calculateSubtotal(subtotal, tax, total);

        this.state = {
            subtotal: subtotal,
            tax: tax,
            total: total,
            additional_comment: ''
        }
        // console.log(this.props.globalState.byo_breakfast);
    }

    componentDidMount() {
        // this.props.getDeliveryMethod();
    }

    handleTextarea = (event) => {
        this.setState({
            additional_comment: event.target.value
        });
        // this.props.handleTextarea(event.target.value);
    }
    
    submitOrder = () => {
        const data = {
            bagelandsheamr: this.props.globalState.bagelandsheamr,
            egg_sandwich: this.props.globalState.egg_sandwich,
            bk_fav: this.props.globalState.bk_fav_reducer,
            sweet_snack: this.props.globalState.sweet_snack,
            beverage: this.props.globalState.beverage,
            fresh_salads: this.props.globalState.fresh_salads,
            cookie: this.props.globalState.cookie,
            lunchsandwich: this.props.globalState.lunchsandwich,
            // breakfast: this.props.globalState.breakfast,
            // byo_breakfast: this.props.globalState.byo_breakfast,
            // lunch: this.props.globalState.lunch,
            // office_party: this.props.globalState.office_party,
            // beverage: this.props.globalState.beverage,
            subtotal: this.state.subtotal,
            tax: this.state.tax,
            total: this.state.total,
            additional_comment: this.state.additional_comment
        };
        this.props.submitOrder(data);
        this.props.history.push('/Confirmation');
    }

    render() {
        return (
            <React.Fragment>
            {
                this.props.globalState.main.delivery_method !== null ?
                <div style={{textAlign: 'center', padding: '100px 0px'}}>
                    <Spinner animation="grow" style={{backgroundColor: '#f58220'}} />
                </div>
                :
                <React.Fragment>
                    {
                        this.props.globalState.main.error.msg === '' ?
                        <Card className="wrap-summary">
                
                            <Card.Header>
                                <TopNav />
                            </Card.Header>

                            <Card.Body>
                                <Card.Title>Order Summary</Card.Title>
                            </Card.Body>

                            {
                                this.state.total === 0 ?
                                <Card.Body className="text-center" style={{marginBottom: '20px'}}>
                                    <Card.Text md={12} style={{paddingTop: '15px', paddingBottom: '15px'}}>
                                        <strong>There are no items in your shopping cart.</strong>
                                    </Card.Text>
                                </Card.Body>
                                :
                                <React.Fragment>

                                    <ListGroup>
                                        <BagelShmear />
                                        <EggSandwich />
                                        <BreakfastFavorites />
                                        <SweetSnack />
                                        <Beverage />
                                        <FreshSalads />
                                        <Cookie />
                                        <LunchSandwiches />
                                    </ListGroup>

                                    <Row className="wrap-total" style={{marginTop: '20px'}}>
                                        <Col className="total-names">Subtotal : </Col><Col className="total-numbers"><span className="number">${this.state.subtotal}</span></Col>
                                    </Row>

                                    <Row className="wrap-total">
                                        <Col className="total-names">Tax : </Col><Col className="total-numbers"><span className="number">${this.state.tax}</span></Col>
                                    </Row>

                                    {
                                        this.props.globalState.main.delivery_method === 'Delivery' ?
                                        <Row className="wrap-total">
                                            <Col className="total-names">Delivery Fee : </Col><Col className="total-numbers"><span className="number">$10</span></Col>
                                        </Row>
                                        :
                                        null
                                    }

                                    <Row className="wrap-total">
                                        <Col className="total-names">Total : </Col><Col className="total-numbers">
                                            <span className="number">
                                                ${
                                                    this.props.globalState.main.delivery_method === 'Delivery' ?
                                                    roundTo(this.state.total+10,2)
                                                    :
                                                    this.state.total
                                                }
                                            </span>
                                        </Col>
                                    </Row>

                                    <ListGroup style={{marginBottom: '40px'}}>
                                        <ListGroup.Item>
                                            <h3 style={{paddingLeft: '0px'}}>Additional Notes (Please provide any dietary restrictions/allergies, etc)</h3>
                                            <textarea style={{width: '100%'}} rows="5" onChange={this.handleTextarea} value={this.state.additional_comment} />
                                        </ListGroup.Item>
                                    </ListGroup>

                                </React.Fragment>
                            }

                            <Row>
                                <Col md={6}>
                                    <ButtonToolbar>
                                        <Link
                                        className={"btn add-btn"}
                                        style={{marginRight: '10px', width:'200px'}}
                                        to="/bagelandshmear"
                                        >
                                        {
                                            this.state.total === 0 ?
                                            'Add Food'
                                            :
                                            'Add More Food'
                                        }
                                        </Link>
                                    </ButtonToolbar>
                                </Col>
                                {
                                    this.state.total === 0 ?
                                    null
                                    :
                                    <Col md={6}>
                                        <ButtonToolbar>
                                            <Button 
                                            className={"btn add-btn"}
                                            style={{marginLeft: 'auto', width:'200px'}} onClick={this.submitOrder}>Submit Order</Button>
                                        </ButtonToolbar>
                                    </Col>
                                }
                            </Row>

                        </Card>
                        :
                        <Card>
                            <Card.Body style={{borderBottomWidth: '0px'}}>
                                <Card.Text>
                                    Your session has expired. Please try again later.
                                </Card.Text>
                            </Card.Body>

                            <Row>
                                <Col md={12}>
                                    <ButtonToolbar>
                                        <a href="/catering/online/agreement.php?r=einsteins" className="btn add-btn btn-lg btn-block" style={{width: '100%'}}>Try Again</a>
                                    </ButtonToolbar>
                                </Col>
                            </Row>
                        </Card>
                    }                    
                </React.Fragment>
                
            }
            </React.Fragment>
        )
    }
}

const mapStateToProps = (state) => ({
    globalState: state
});

const mapDispatchToProps = dispatch => (
    {
        // updateQty: (name, qty) => dispatch(updateQty(name, qty))
        updateCurrentNav: (index) => dispatch(updateCurrentNav(index)),
        calculateSubtotal: (subtotal, tax, total) => dispatch(calculateSubtotal(subtotal, tax, total)),
        submitOrder: (data) => dispatch(submitOrder(data)),
        getDeliveryMethod: () => dispatch(getDeliveryMethod()),
        handleTextarea: (text) => dispatch(handleTextarea(text))
    }
)

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Summary)