import React from 'react';
import { connect } from "react-redux";
import { Row, Col, ListGroup } from 'react-bootstrap';

class LunchSandwiches extends React.Component {
    
    printList = (key, name) => {
        return (
            <li className="summary-list" style={{display: (this.props.lunchsandwich[key].qty === 0 ? 'none' : null)}}>
                <Row>
                    <Col className="item" md={5}>{name}</Col>
                    <Col className="qty">{this.props.lunchsandwich[key].qty}</Col>
                    <Col className="price">${this.props.lunchsandwich[key].price}</Col>
                    <Col className="total">${this.props.lunchsandwich[key].total}</Col>
                </Row>
            </li>
        )
    }

    printListWithSub = (box, type, name) => {
        let print_subitem = [];
        let subitem_index = 0;
        let box_count = 1;
        const names = this.props.lunchsandwich[box].items;
        
        for (let list_id in this.props.lunchsandwich[box][type].list){
            let tmp_list = []
            for (let key in this.props.lunchsandwich[box][type].list[list_id]){
                let name = names[key].name;
                if (this.props.lunchsandwich[box][type].list[list_id][key] !== 0){
                    tmp_list.push(<li className="" key={'_child_' + subitem_index}> { name + ' : ' + this.props.lunchsandwich[box][type].list[list_id][key] } </li>)
                    subitem_index++;
                }
            }
            print_subitem.push(
                <Col md={6} style={{padding: '5px'}} key={'lunchsandwich_box'+list_id}>
                    <div style={{padding: '5px', borderBottom: '1px solid #e1e1e1'}}>
                        <ul className="list-inline" style={{paddingLeft: '10px', listStyle: 'none'}}> 
                            <h3 className="p-0" style={{fontSize: '1rem'}}>{box}-{type} #{box_count}</h3>
                            {tmp_list} 
                        </ul>
                    </div>
                </Col>
            )
            box_count++;
        }

        return (
            <React.Fragment>

                <Row>
                    <Col className="item" md={5}>{name}</Col>
                    <Col className="qty">{this.props.lunchsandwich[box][type].qty}</Col>
                    <Col className="price">${this.props.lunchsandwich[box][type].price}</Col>
                    <Col className="total">${this.props.lunchsandwich[box][type].total}</Col>
                </Row>

                <Row className="item sub" style={{paddingTop: '10px'}}>
                    { print_subitem }
                </Row>

            </React.Fragment>
        )
    }

    printListWithChoices = (box) => {
        let print = [];
        let box_count = 0;
        const list = this.props.lunchsandwich[box].list;
        for (let box_id in list){
            let print_li = [];
            let tmp_count = 0;

            const items = ['sandwich', 'cookie', 'chip', 'drink'];

            for (let item_id in items){
                const item = items[item_id];
                for (let key in list[box_id][item]){
                    if (list[box_id][item][key] !== 0){
                        print_li.push(<li className="" key={'_child_' + tmp_count}> { this.props.lunchsandwich[box].items[item][key].name + ' : ' + list[box_id][item][key] } </li>)
                        tmp_count++;
                    }
                }
            }

            print.push(
                <Col md={6} style={{padding: '5px'}} key={'ex_box' + box_count}>
                    <div key={box_count} className="" style={{padding: '5px', borderBottom: '1px solid #e1e1e1'}}>
                        <h3 className="p-0" style={{fontSize: '1rem'}}>{box} #{box_count+1}
                        </h3>
                        <ul key={box_count} className="" style={{height: '', paddingLeft: '10px', listStyle: 'none'}}> {print_li}{"Chocolate Chip Cookies : 12"}  </ul>
                    </div>
                </Col>
            )
            box_count++;
        }
        return print;
    }

    printLunchBox = (box, name) => {
        let print_subitem = [];
        // let subitem_index = 0;
        // const names = this.props.lunchsandwich.lunchBox.cookie;

        const list = this.props.lunchsandwich.lunchBox[box].list;

        let count = 1;
        for (let list_id in list){

            const items = list[list_id]
            
            let chip_name = this.props.lunchsandwich.lunchBox.chips;
            let cookie_name = this.props.lunchsandwich.lunchBox.cookie;

            print_subitem.push(
                <Col md={3} style={{padding: '5px'}} key={'ex_box' + list_id}>
                    <div key={list_id} className="" style={{padding: '5px', borderBottom: '1px solid #e1e1e1'}}>
                        <h3 className="p-0" style={{fontSize: '1rem'}}>#{count} {box}</h3>
                        <ul key={list_id} className="" style={{height: '', paddingLeft: '10px', listStyle: 'none'}}>
                            { items.chip !== null ? <li className=""> { chip_name[items.chip].name } </li> : null }
                            { items.fruit !== null ? <li className=""> Fruit </li> : null }
                            { items.cookie !== null ? <li className=""> { cookie_name[items.cookie].name } </li> : null }
                            {<li>Chocolate Chip Cookie</li>}
                        </ul>
                    </div>
                </Col>
            )
            count++;
        }

        return (
            <React.Fragment>

                <Row>
                    <Col className="item" md={5}>{name}</Col>
                    <Col className="qty">{this.props.lunchsandwich.lunchBox[box].qty}</Col>
                    <Col className="price">${this.props.lunchsandwich.lunchBox[box].price}</Col>
                    <Col className="total">${this.props.lunchsandwich.lunchBox[box].total}</Col>
                </Row>

                <Row className="item sub" style={{paddingTop: '10px'}}>
                    { print_subitem }
                </Row>

            </React.Fragment>
        )
    }

    render() {
        return (
            <ListGroup.Item style={{display: (this.props.lunchsandwich.total === 0 ? 'none' : null)}}>
                <h3>Lunch Sandwiches</h3>
                <ul className="summary-list-group">
                    <li className="summary-list header">
                        <Row>
                            <Col className="item" md={5}>Item</Col>
                            <Col className="qty">Quantity</Col>
                            <Col className="price">Price</Col>
                            <Col className="total">Total</Col>
                        </Row>
                    </li>

                    <li className="summary-list" style={{display: (this.props.lunchsandwich.signature.full.qty === 0 ? 'none' : null)}}>
                        {this.printListWithSub('signature', 'full', 'Signature Lunch Nosh Box - 10 Sandwiches')}
                    </li>

                    <li className="summary-list" style={{display: (this.props.lunchsandwich.signature.half.qty === 0 ? 'none' : null)}}>
                        {this.printListWithSub('signature', 'half', 'Signature Lunch Nosh Box - 5 Sandwiches')}
                    </li>

                    <li className="summary-list" style={{display: (this.props.lunchsandwich.classic.full.qty === 0 ? 'none' : null)}}>
                        {this.printListWithSub('classic', 'full', 'Classic Lunch Nosh Box - 10 Sandwiches')}
                    </li>

                    <li className="summary-list" style={{display: (this.props.lunchsandwich.classic.half.qty === 0 ? 'none' : null)}}>
                        {this.printListWithSub('classic', 'half', 'Classic Lunch Nosh Box - 5 Sandwiches')}
                    </li>



                    <li className="summary-list" style={{display: (this.props.lunchsandwich.forGroup.qty === 0 ? 'none' : null)}}>
                        <Row>
                            <Col className="item" md={5}>Lunch For The Group</Col>
                            <Col className="qty">{this.props.lunchsandwich.forGroup.qty}</Col>
                            <Col className="price">${this.props.lunchsandwich.forGroup.price}</Col>
                            <Col className="total">${this.props.lunchsandwich.forGroup.total}</Col>
                        </Row>
                        <Row className="item sub" style={{paddingTop: '10px'}}>
                            {this.printListWithChoices('forGroup')}
                        </Row>
                    </li>

                    <li className="summary-list" style={{display: (this.props.lunchsandwich.lunchBox.turkey.qty === 0 ? 'none' : null)}}>
                        {this.printLunchBox('turkey', 'Lunch Box - Turkey & Cheddar')}
                    </li>

                    <li className="summary-list" style={{display: (this.props.lunchsandwich.lunchBox.harvest.qty === 0 ? 'none' : null)}}>
                        {this.printLunchBox('harvest', 'Lunch Box - Harvest Chicken Salad')}
                    </li>

                    <li className="summary-list" style={{display: (this.props.lunchsandwich.lunchBox.ham.qty === 0 ? 'none' : null)}}>
                        {this.printLunchBox('ham', 'Lunch Box - Ham & Swiss')}
                    </li>

                    {/*<li className="summary-list" style={{display: (this.props.lunchsandwich.lunchBox.albacore.qty === 0 ? 'none' : null)}}>
                        {this.printLunchBox('albacore', 'Lunch Box - Albacore Tuna Salad')}
                    </li>*/}

                    <li className="summary-list" style={{display: (this.props.lunchsandwich.lunchBox.tasty.qty === 0 ? 'none' : null)}}>
                        {this.printLunchBox('tasty', 'Lunch Box - Tasty Turkey')}
                    </li>

                    <li className="summary-list" style={{display: (this.props.lunchsandwich.lunchBox.avocado.qty === 0 ? 'none' : null)}}>
                        {this.printLunchBox('avocado', 'Lunch Box - Avocado Veg Out')}
                    </li>

                    <li className="summary-list" style={{display: (this.props.lunchsandwich.lunchBox.green.qty === 0 ? 'none' : null)}}>
                        {this.printLunchBox('green', 'Lunch Box - Green Chile Club')}
                    </li>

                    <li className="summary-list" style={{display: (this.props.lunchsandwich.lunchBox.nova.qty === 0 ? 'none' : null)}}>
                        {this.printLunchBox('nova', 'Lunch Box - Nova Lox')}
                    </li>






                    {/*{this.printList('nova_lox', 'Nova Lox')}*/}
                    
                    <li className="summary-list footer">
                        <Row>
                            <Col className="item" md={5}></Col>
                            <Col className="qty"></Col>
                            <Col className="price"></Col>
                            <Col className="total">${this.props.lunchsandwich.total}</Col>
                        </Row>
                    </li>
                </ul>
            </ListGroup.Item>
        )
    }
}

const mapStateToProps = (state) => ({
    lunchsandwich: state.lunchsandwich
});

export default connect(
    mapStateToProps
)(LunchSandwiches)