import React from 'react';
import { connect } from "react-redux";

import { Row, Col, Card, ButtonToolbar, Button, ListGroup } from 'react-bootstrap';
import NumberInput from '../common/numberInput.js';

import { addBox, closeBox, updateBox } from '../../redux/actions/cookie_action';

class Chips extends React.Component {
    constructor(props) {
        super(props);

        let list = {};

        if (this.props.mode === 'edit'){ 
            list = this.props.cookie[this.props.box][this.props.type].list[this.props.id]; 
        }
        else {
            for (const key in this.props.cookie.kettle.items){
                list[key] = 0;
            }
        }


        this.state = {
            //qty: 0, //when editing an order quantity shown to user is 0, want it to be max quantity. May be able to fix this in functions below 
            qty : (this.props.mode === 'edit' ? (this.props.type === 'single' ? 1 : this.props.type ==='small' ? 5 : 10) : 0),
            list: list,
            error: false,
            error_msg: '',
            max_qty: (this.props.type === 'single' ? 1 : this.props.type ==='small' ? 5 : 10)
        }
        
    }

    onChangeInput = (name, qty) => {
        this.setState({error: false})

        let list = this.state.list;
        // list[name] = qty;
        const max_qty = this.state.max_qty;
        let tmp_qty = 0;
        for (let key in list){
            if (key !== name){
                tmp_qty = tmp_qty + list[key];    
            }
            else{
                tmp_qty = tmp_qty + qty;
            }
        }
        
        if (tmp_qty <= max_qty){
            list[name] = qty;
            this.setState({
                list: list,
                qty: tmp_qty
            })
        }
        else {
            this.setState({
                error: true,
                error_msg: 'qty max error'
            })
            return 'error';
        }
    }

    closeBox = () => {
        this.props.closeBox();
    }

    addBox = () => {
        if (this.validateBox()){

        }
        else {
            this.props.addBox(this.state.list);
        }
    }

    updateBox = () => {
        if (this.validateBox()){

        }
        else {
            this.props.updateBox(this.state.list);
        }
    }

    validateBox = () => {
        let error = false;        
        if (this.state.qty !== this.state.max_qty){
            this.setState({error: true});
            error = true;
        }
        return error;
    }

    printItem = () => {
        let print = [];
        let index = 0;
        const items = this.props.cookie.kettle.items;
        for (let key in items) {
            print.push(
                <Col md={12} className="wrap_qty mb-3" key={'kettle_input'+index}>
                    {items[key].name}
                    <NumberInput 
                        readonly={true}
                        onChange={this.onChangeInput.bind(this)}
                        name={key}
                        value={this.state.list[key]}
                        style={{float: 'left', textAlign: 'left'}}
                    />
                </Col>
            )
            index++;
        }

        return print;
    }

    render() {
        return(
            <Card className="col-md-12 p-0">
                <Card.Header>

                </Card.Header>

                <Card.Body>
                    <Card.Title>
                        Select Your Chips
                        <span style={{ textTransform: 'initial', marginLeft: '12px', fontSize: '17px', color: '#545454'}}>
                        {
                            this.props.type === 'single' ?
                            'Select 1'
                            :
                            this.props.type === 'small' ? 'Select 5' : 'Select 10'
                        }
                        </span>
                    </Card.Title>
                </Card.Body>

                <Card.Body className={this.state.error ? 'su-alert' : ''}>
                    <Card.Text>You selected <strong>{this.state.qty} of {this.state.max_qty}</strong> Chips.</Card.Text>
                </Card.Body>

                <ListGroup variant="flush">

                    <ListGroup.Item className="menu-list">
                        <Row className="mb-3">

                            { this.printItem() }

                        </Row>

                        <Row>
                            <Col>
                                <ButtonToolbar className="mt-4">
                                    {
                                        this.props.mode === 'add' ?
                                        <Button variant="primary" size="md" onClick={this.addBox.bind(this)} className="add-btn">
                                            Add
                                        </Button>
                                        :
                                        <Button variant="primary" size="md" onClick={this.updateBox.bind(this)} className="add-btn">
                                            Update
                                        </Button>
                                    }
                                    <Button variant="light" size="md" onClick={this.closeBox.bind(this)} className="cancel-btn ml-2">
                                        Cancel
                                    </Button>
                                </ButtonToolbar>
                            </Col>
                        </Row>

                    </ListGroup.Item>

                </ListGroup>

            </Card>
        )
    }
}

const mapStateToProps = (state) => ({
    cookie: state.cookie
});

const mapDispatchToProps = dispatch => (
    {
        addBox: (list) => dispatch(addBox(list)),
        closeBox: () => dispatch(closeBox()),
        updateBox: (id, list) => dispatch(updateBox(id, list))
    }
)

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Chips)