import {
    UPDATE_SWEETSNACK_QTY,
    OPEN_SWEETSNACK_BOX,
    OPEN_EDIT_SWEETSNACK_BOX,
    CLOSE_SWEETSNACK_BOX,
    ADD_SWEETSNACK_BOX,
    UPDATE_SWEETSNACK_BOX,
    REMOVE_SWEETSNACK_BOX
} from './actionTypes.js'

export const updateQty = (name, qty) => ({
    type: UPDATE_SWEETSNACK_QTY,
    payload: {
        name,
        qty
    }
})

export const openBox = (name, mode) => ({
    type: OPEN_SWEETSNACK_BOX,
    payload: {
        name,
        mode
    }
})

export const openEditBox = (box, type, id) => ({
    type: OPEN_EDIT_SWEETSNACK_BOX,
    payload: {
        box,
        type,
        id
    }
})

export const closeBox = () => ({
    type: CLOSE_SWEETSNACK_BOX
})

export const addBox = (list) => ({
    type: ADD_SWEETSNACK_BOX,
    payload: {
        list
    }
})

export const updateBox = (list) => ({
    type: UPDATE_SWEETSNACK_BOX,
    payload: {
        list
    }
})

export const removeBox = (box, type, id) => ({
    type: REMOVE_SWEETSNACK_BOX,
    payload: {
        box,
        type,
        id
    }
})