import { 
    OPEN_EGG_SANDWICH_BOX,
    OPEN_EDIT_EGG_SANDWICH_BOX,
    CLOSE_EGG_SANDWICH_BOX,
    ADD_EGG_SANDWICH_BOX,
    UPDATE_EGG_SANDWICH_BOX,
    REMOVE_EGG_SANDWICH_BOX
} from '../actions/actionTypes';

const initialState = {
    box: null,
    type: null,
    mode: null,
    id: null,
    classic: {
        name: "Classic Egg Sandwich Nosh Box",
        description: "An assortment of Classic Egg Sandwiches including: Applewood Bacon & Cheddar, Turkey-Sausage & Cheddar, Ham & Swiss, and Garden Avocado.",
        items: {
            applewood: {name: 'Applewood Bacon & Cheddar'},
            turkey: {name: 'Turkey-Sausage & Cheddar'},
            ham: {name: 'Ham & Swiss'},
            garden: {name: 'Garden Avocado on Everything Bagel'}
        },
        half: {
            price: 34.99,
            qty: 0,
            total: 0,
            list: [],
            selected: true
        },
        dozen: {
            price: 68.99,
            qty: 0,
            total: 0,
            list: [],
            selected: true
        },
        total: 0
    },
    signature: {
        name: "Signature Egg Sandwich Nosh Box",
        description: "An assortment of Signature Egg Sandwiches including: The Farmhouse, Applewood Bacon & Cheddar, Turkey-Sausage & Cheddar, Ham & Swiss, and Garden Avocado",
        items: {
            farmhouse: {name: 'Farmhouse on Hashbrown Bagel'},
            applewood: {name: 'Applewood Bacon & Cheddar on Asiago'},
            turkey: {name: 'Turkey-Sausage & Cheddar on Plain Bagel'},
            ham: {name: 'Ham & Swiss'},
            garden: {name: 'Garden Avocado on Everything Bagel'}
        },
        half: {
            price: 34.99,
            qty: 0,
            total: 0,
            list: [],
            selected: true
        },
        dozen: {
            price: 68.99,
            qty: 0,
            total: 0,
            list: [],
            selected: true
        },
        total: 0
    },
    thintastic: {
        name: "Thintastic Egg Whites Sandwich Nosh Box",
        description: "Thintastic Egg Sandwiches: Thintastic Santa Fe (Egg white, Turkey-Sausage, Cheddar Cheese with Roasted Tomato Salsa and Jalapeno Salsa Shmear on an Asiago Thintastic Bagel) and Bacon, Avocado & Tomato (Egg white, Bacon, Avocado, Tomato and Roasted Tomato spread on a Plain Thintastic Bagel).",
        items: {
            santafe: {name: 'Santa Fe Turkey-Sausage, Cheddar, Egg White, Roasted Tomato Salsa & Jalapeno Salsa Schmear on Asiago Thintastic Bagel'},
            bacon: {name: 'Bacon, Tomato, Avocado, Egg White & Roasted Tomato spread on a Plain Thintastic Bagel'}
        },
        half: {
            price: 34.99,
            qty: 0,
            total: 0,
            list: [],
            selected: true
        },
        dozen: {
            price: 69.99,
            qty: 0,
            total: 0,
            list: [],
            selected: true
        },
        total: 0
    },
    total: 0
}

const roundTo = require('roundto');

export default function(state = initialState, action) {
    switch (action.type) {
        case OPEN_EGG_SANDWICH_BOX : {
            const {name, mode} = action.payload;
            const [box, type] = name.split('_');

            state.box = box;
            state.type = type;
            state.mode = mode;

            return {...state};
        }
        case OPEN_EDIT_EGG_SANDWICH_BOX : {
            const {box, type, id} = action.payload;

            state.box = box;
            state.type = type;
            state.id = id;
            state.mode = 'edit';

            return {...state}
        }
        case ADD_EGG_SANDWICH_BOX : {
            const {list} = action.payload;

            state[state.box][state.type].list.push(list);                                   
            state[state.box][state.type].qty = state[state.box][state.type].qty + 1;

            // Get Subtotal
            const box_total = state[state.box][state.type].total;
            state[state.box][state.type].total = roundTo(state[state.box][state.type].qty * state[state.box][state.type].price, 2);
            const diff = state[state.box][state.type].total - box_total;

            // Get Total
            state.total = roundTo(state.total + diff, 2);

            // Reset box, type, mode
            state.box = null;
            state.type = null;
            state.mode = null;

            return {...state}
        }
        case UPDATE_EGG_SANDWICH_BOX : {
            const {list} = action.payload;

            state[state.box][state.type].list[state.id] = list;

            // Reset box, type, mode
            state.box = null;
            state.type = null;
            state.mode = null;

            return {...state}
        }
        case REMOVE_EGG_SANDWICH_BOX: {
            const {box, type, id} = action.payload;

            state[box][type].list[id] = null;
            state[box][type].qty = state[box][type].qty - 1;

            // Subtotal
            const box_total = state[box][type].total;
            state[box][type].total = roundTo(state[box][type].qty * state[box][type].price, 2);
            const diff = state[box][type].total - box_total;

            // Get Total
            state.total = roundTo(state.total + diff, 2);

            return {...state}
        }
        case CLOSE_EGG_SANDWICH_BOX: {
            state.box = null;
            state.type = null;
            state.mode = null;

            return {...state};
        }
        default :
            return state;
    }
}