import {
    UPDATE_COOKIE_QTY,
    OPEN_COOKIE_BOX,
    OPEN_EDIT_COOKIE_BOX,
    CLOSE_COOKIE_BOX,
    ADD_COOKIE_BOX,
    UPDATE_COOKIE_BOX,
    REMOVE_COOKIE_BOX,
    ADD_KETTLE_CHIP,
    UPDATE_KETTLE_CHIP,
    CLOSE_KETTLE_CHIP,
    REMOVE_KETTLE_CHIP
} from './actionTypes.js'

export const updateQty = (name, qty) => ({
    type: UPDATE_COOKIE_QTY,
    payload: {
        name,
        qty
    }
})

export const openBox = (name, mode) => ({
    type: OPEN_COOKIE_BOX,
    payload: {
        name,
        mode
    }
})

export const openEditBox = (box, type, id) => ({
    type: OPEN_EDIT_COOKIE_BOX,
    payload: {
        box,
        type,
        id
    }
})

export const closeBox = () => ({
    type: CLOSE_COOKIE_BOX
})

export const addBox = (list) => ({
    type: ADD_COOKIE_BOX,
    payload: {
        list
    }
})

export const updateBox = (list) => ({
    type: UPDATE_COOKIE_BOX,
    payload: {
        list
    }
})

export const removeBox = (box, type, id) => ({
    type: REMOVE_COOKIE_BOX,
    payload: {
        box,
        type,
        id
    }
})

export const closeChip = () => ({
    type: CLOSE_KETTLE_CHIP
})

export const addChip = (item) => ({
    type: ADD_KETTLE_CHIP,
    payload: {
        item
    }
})

export const updateChip = (item) => ({
    type: UPDATE_KETTLE_CHIP,
    payload: {
        item
    }
})

export const removeChip = (type, item) => ({
    type: REMOVE_KETTLE_CHIP,
    payload: {
        type,
        item
    }
})