import React from 'react';
import { connect } from "react-redux";

import { Row, Col, Card, ButtonToolbar, Button, ListGroup, Modal } from 'react-bootstrap';
// import NumberInput from '../common/numberInput.js';

import { addLunchBox, closeBox, updateLunchBox } from '../../redux/actions/lunchsandwich_action';

class Cookie extends React.Component {
    constructor(props) {
        super(props);

        let selected = {
            chip: null,
            fruit: null,
            cookie: null
        };

        if (this.props.mode === 'edit'){ 
            selected = this.props.lunchsandwich.lunchBox[this.props.type].list[this.props.id]; 
        }

        this.state = {
            error: false,
            error_msg: '',
            num_selected: this.props.mode === 'edit' ? 1 : 0,
            selected: selected,
            max_qty: 1
        }
        
    }

    closeBox = () => {
        this.props.closeBox();
    }

    addLunchBox = () => {
        if (this.state.num_selected !== 1){
            this.displayErr();
        }
        else {
            this.props.addLunchBox(this.state.selected);
        }
    }

    updateLunchBox = () => {
        if (this.state.num_selected !== 1){
            this.displayErr();
        }
        else {
            this.props.updateLunchBox(this.state.selected);
        }
    }

    displayErr = () => {
        this.setState({
            error: true
        })
    }

    
    onSelectItem = (type, key) => {
        let pass = true;
        let num = this.state.num_selected;
        
        if (this.state.selected[type] === key){
            key = null;
            num = num - 1;
        }
        else if (this.state.num_selected === 1 && this.state.selected[type] !== key){//replacing null for 'key' in this elseif statement makes it so user can select chips and cookies. The final option should say (this.state.num_selected === final_number && this.state.selected[type] !== key) and other options will say (this.state.num_selected === option_number && this.state.selected[type] !== null)
            pass = false;
        }
        //else if (this.state.num_selected === 2 && this.state.selected[type] !== key){
            //(this.state.num_selected ===2 && this.state.selected[type] !== key) //replacing 'key' for null in the above elseif statement makes it so user can select chips OR cookies, not both. Set to null for now so users can select both a chip and cookie option for 'Lunch Boxes'
        //    pass = false;
        //}
        //else if (this.state.num_selected === 3 && this.state.selected[type] !== key){
            //added this third elseif statement after the edit above where '&& this.state.selected[type] !== key' was replaced with '&& this.state.selected[type] !== null'
        //    pass = false;
        //}
        else{
            num = num + 1;
        }

        if (pass){
            this.setState({
                num_selected: num,
                selected: {
                    ...this.state.selected,
                    [type]: key
                }
            });
        }
    }

    printChips = () => {
        let print = [];
        let index = 0;
        for (let key in this.props.lunchsandwich.lunchBox.chips){
            const name = this.props.lunchsandwich.lunchBox.chips[key].name;
            print.push(
                <Button onClick={this.onSelectItem.bind(this, 'chip', key)} className={(this.state.selected.chip === key ? 'btn-select' : 'btn-unselect') + ' mr-1'} key={'item_'+index}>
                    {name}
                </Button>
            )
            index++;
        }

        return (
                <ButtonToolbar>
                    {print}
                </ButtonToolbar>
            );
    }

    printCookies = () => {
        let print = [];
        let index = 0;
        for (let key in this.props.lunchsandwich.lunchBox.cookie){
            const name = this.props.lunchsandwich.lunchBox.cookie[key].name;
            print.push(
                <Button onClick={this.onSelectItem.bind(this, 'cookie', key)} className={(this.state.selected.cookie === key ? 'btn-select' : 'btn-unselect') + ' mr-1'} key={'item_'+index}>
                    {name}
                </Button>
            )
            index++;
        }

        return (
                <ButtonToolbar>
                    {print}
                </ButtonToolbar>
            );
    }

    render() {
        return(
            <Modal show={true} className="lunchsandwich_lunchbox_wrap">
                <Modal.Header style={{fontWeight: '600'}}>
                    Choose your items
                </Modal.Header>

                <Modal.Body style={{padding: '5px'}}>
                
                    <Card.Body className={this.state.error ? 'su-alert' : ''}>
                        <Card.Text>You selected <strong>{this.state.num_selected} of {this.state.max_qty}</strong> Items.</Card.Text>
                    </Card.Body>

                    <ListGroup >

                        <ListGroup.Item className="menu-list es-list-group-item" style={{paddingTop: '0px', marginTop: '0px', borderWidth: '0px'}}>

                            <Row style={{marginTop: '25px'}}>
                                <Col className="wrap_name">
                                    <p className="item-header" style={{marginBottom: '0px', paddingLeft: '0px', color: '#b36a3b'}}>Chips</p>
                                </Col>
                            </Row>

                            <Row className="mt-3 mb-3">
                                <Col md={12}>
                                    {
                                        this.printChips()
                                    }
                                </Col>
                            </Row>

                        </ListGroup.Item>

                        <ListGroup.Item className="menu-list es-list-group-item" style={{paddingTop: '0px', marginTop: '0px', borderWidth: '0px'}}>

                            <Row style={{marginTop: '25px'}}>
                                <Col className="wrap_name">
                                    <p className="item-header" style={{marginBottom: '0px', paddingLeft: '0px', color: '#b36a3b'}}>Fruit</p>
                                </Col>
                            </Row>

                            <Row className="mt-3 mb-3">
                                <Col md={12}>
                                    <Button onClick={this.onSelectItem.bind(this, 'fruit', 'fruit')} className={(this.state.selected.fruit !== null  ? 'btn-select' : 'btn-unselect') + ' mr-1'} >
                                        Fruit
                                    </Button>
                                </Col>
                            </Row>

                        </ListGroup.Item>

                        {/* <ListGroup.Item className="menu-list es-list-group-item" style={{paddingTop: '0px', marginTop: '0px', borderWidth: '0px'}}>

                            <Row style={{marginTop: '25px'}}>
                                <Col className="wrap_name">
                                    <p className="item-header" style={{marginBottom: '0px', paddingLeft: '0px', color: '#b36a3b'}}>Cookie</p>
                                </Col>
                            </Row>

                            <Row className="mt-3 mb-3">
                                <Col md={12}>
                                    {
                                        this.printCookies()
                                    }
                                </Col>
                            </Row>

                        </ListGroup.Item> */}

                    </ListGroup>
                </Modal.Body>

                <Modal.Footer style={{justifyContent: 'left'}}>
                    <ButtonToolbar className="">
                        {
                            this.props.mode === 'add' ?
                            <Button variant="primary" onClick={this.addLunchBox.bind(this)} className="add-btn">
                                Add
                            </Button>
                            :
                            <Button variant="primary" onClick={this.updateLunchBox.bind(this)} className="add-btn">
                                Update
                            </Button>
                        }
                        <Button variant="light" onClick={this.closeBox.bind(this)} className="cancel-btn ml-2">
                            Cancel
                        </Button>
                    </ButtonToolbar>
                </Modal.Footer>

                {/* <Card className="col-md-12 p-0">
                    <Card.Header>

                    </Card.Header>

                    <Card.Body>
                        <Card.Title>
                            Choose your Gourmet Cookie
                        </Card.Title>
                    </Card.Body>

                    <ListGroup >

                        <ListGroup.Item className="menu-list es-list-group-item">

                            <Row style={{marginTop: '25px'}}>
                                <Col className="wrap_name">
                                    <p className="item-header" style={{marginBottom: '0px', paddingLeft: '0px', color: '#b36a3b'}}>Bagel</p>
                                </Col>
                            </Row>

                            <Row className="mt-3 mb-3">
                                <Col md={12}>
                                    {
                                        this.printCookies()
                                    }
                                </Col>
                            </Row>

                        </ListGroup.Item>

                    </ListGroup>

                    <Row>
                        <Col>
                            <ButtonToolbar className="mt-4">
                                {
                                    this.props.mode === 'add' ?
                                    <Button variant="primary" size="md" onClick={this.addBox.bind(this)} className="add-btn">
                                        Add
                                    </Button>
                                    :
                                    <Button variant="primary" size="md" onClick={this.updateBox.bind(this)} className="add-btn">
                                        Update
                                    </Button>
                                }
                                <Button variant="light" size="md" onClick={this.closeBox.bind(this)} className="cancel-btn ml-2">
                                    Cancel
                                </Button>
                            </ButtonToolbar>
                        </Col>
                    </Row>

                </Card> */}
            </Modal>
        )
    }
}

const mapStateToProps = (state) => ({
    lunchsandwich: state.lunchsandwich
});

const mapDispatchToProps = dispatch => (
    {
        addLunchBox: (item) => dispatch(addLunchBox(item)),
        closeBox: () => dispatch(closeBox()),
        updateLunchBox: (item) => dispatch(updateLunchBox(item))
    }
)

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Cookie)