import React from 'react';
import { connect } from "react-redux";

import { Row, Col, Card, ButtonToolbar, Button, ListGroup } from 'react-bootstrap';
// import NumberInput from '../common/numberInput.js';

import { addBox, closeBox, updateBox } from '../../redux/actions/fresh_salads_action';

class Choice extends React.Component {
    constructor(props) {
        super(props);

        let selected_item = null;

        if (this.props.mode === 'edit'){ 
            selected_item = this.props.fresh_salads[this.props.box].list[this.props.id]; 
        }

        this.state = {
            selected_item: selected_item,
            error: false,
            error_msg: ''
        }
        
    }

    closeBox = () => {
        this.props.closeBox();
    }

    addBox = () => {
        if (this.state.selected_item === null){
            this.displayErr();
        }
        else {
            this.props.addBox(this.state.selected_item);
        }
    }

    updateBox = () => {
        if (this.state.selected_item === null){
            this.displayErr();
        }
        else {
            this.props.updateBox(this.state.selected_item);
        }
    }

    displayErr = () => {
        this.setState({
            error: true
        })
    }

    onSelectItem = (key) => {
        this.setState({
            selected_item: key
        });
    }

    printItem = (type, key, index) => {
        const name = this.props.fresh_salads.items[type][key].name;

        return (
            <Button onClick={this.onSelectItem.bind(this, key)} className={(this.state.selected_item === key ? 'btn-select' : 'btn-unselect') + ' mr-1'} key={'item_'+type+'_'+index}>
                {name}
            </Button>
        );
    }

    printItemsByType = (type) => {
        let print = [];
        let index = 0;
        for (let key in this.props.fresh_salads.items[type]){
            print.push(
                this.printItem(type, key, index)
            )
            index++;
        }

        return (
                <ButtonToolbar>
                    {print}
                </ButtonToolbar>
            );
    }

    render() {
        return(
            <Card className="col-md-12 p-0">
                <Card.Header>
                    <p className="item-header" style={{paddingLeft: '10px', fontSize: '20px', color: '#b36a3b'}}>
                        {this.props.fresh_salads[this.props.box].name}
                        {
                            this.props.box === 'chicken' ?
                            "Strawberry Chicken Salad"
                            :
                            "Strawberry Almond Salad"
                        }
                    </p>
                </Card.Header>

                {/* <Card.Body className={this.state.error ? 'su-alert' : ''}>
                    <Card.Text>You selected <strong>{this.state.qty} of {this.state.max_qty}</strong> Sandwiches.</Card.Text>
                </Card.Body> */}

                <ListGroup >

                    <ListGroup.Item className="menu-list es-list-group-item">

                        <Row style={{marginTop: '25px'}}>
                            <Col className="wrap_name">
                                <p className="item-header" style={{marginBottom: '0px', paddingLeft: '0px', color: '#b36a3b'}}>Bagel</p>
                            </Col>
                        </Row>

                        <Row className="mt-3 mb-3">
                            <Col md={12}>
                                {
                                    this.printItemsByType('bagel')
                                }
                            </Col>
                        </Row>

                    </ListGroup.Item>

                    <ListGroup.Item className="menu-list es-list-group-item">

                        <Row style={{marginTop: '25px'}}>
                            <Col className="wrap_name">
                                <p className="item-header" style={{marginBottom: '0px', paddingLeft: '0px', color: '#b36a3b'}}>Chips</p>
                            </Col>
                        </Row>

                        <Row className="mt-3 mb-3">
                            <Col md={12}>
                                {
                                    this.printItemsByType('chips')
                                }
                            </Col>
                        </Row>

                    </ListGroup.Item>

                </ListGroup>

                <Row>
                    <Col>
                        <ButtonToolbar className="mt-4">
                            {
                                this.props.mode === 'add' ?
                                <Button variant="primary" size="md" onClick={this.addBox.bind(this)} className="add-btn">
                                    Add
                                </Button>
                                :
                                <Button variant="primary" size="md" onClick={this.updateBox.bind(this)} className="add-btn">
                                    Update
                                </Button>
                            }
                            <Button variant="light" size="md" onClick={this.closeBox.bind(this)} className="cancel-btn ml-2">
                                Cancel
                            </Button>
                        </ButtonToolbar>
                    </Col>
                </Row>

            </Card>
        )
    }
}

const mapStateToProps = (state) => ({
    fresh_salads: state.fresh_salads
});

const mapDispatchToProps = dispatch => (
    {
        addBox: (item) => dispatch(addBox(item)),
        closeBox: () => dispatch(closeBox()),
        updateBox: (item) => dispatch(updateBox(item))
    }
)

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Choice)