const roundTo = require('roundto');

export default function(state = initialState, action) {
    switch (action.type) {
        case UPDATE_BEVERAGE_QTY: {
            const {name, qty} = action.payload;

            const total = state.list[name].total;
            state.list[name].qty = qty;
            state.list[name].total = roundTo(state.list[name].price * qty, 2);
            const diff = roundTo(state.list[name].total - total, 2);

            state.total = roundTo(state.total + diff, 2);

            return {...state};
        }
        default:
            return state;
    }
};

