import React from 'react';
import { connect } from "react-redux";
import { Row, Col, ListGroup } from 'react-bootstrap';

class Cookie extends React.Component {
    
    printList = (key, name) => {
        return (
            <li className="summary-list" style={{display: (this.props.cookie[key].qty === 0 ? 'none' : null)}}>
                <Row>
                    <Col className="item" md={5}>{name}</Col>
                    <Col className="qty">{this.props.cookie[key].qty}</Col>
                    <Col className="price">${this.props.cookie[key].price}</Col>
                    <Col className="total">${this.props.cookie[key].total}</Col>
                </Row>
            </li>
        )
    }

    printListWithSub = (box, type, name) => {
        let print_subitem = [];
        let subitem_index = 0;
        let box_count = 1;
        const names = box === 'kettle' ? this.props.cookie.kettle.items : this.props.cookie.items;
        
        //type = box === 'variety_half' ? 'half' : 'dozen';
        //box = box === 'sweet' ? 'sweet' : 'variety';
        
        for (let list_id in this.props.cookie[box][type].list){
            let tmp_list = []
            for (let key in this.props.cookie[box][type].list[list_id]){
                if (this.props.cookie[box][type].list[list_id][key] > 0){
                    let name = names[key].name;
                    tmp_list.push(<li className="" key={'_child_' + subitem_index}> { name + ' : ' + this.props.cookie[box][type].list[list_id][key] } </li>)
                    subitem_index++;
                }
            }
            print_subitem.push(
                <Col md={6} style={{padding: '5px'}} key={'cookie_box'+list_id}>
                    <div style={{padding: '5px', borderBottom: '1px solid #e1e1e1'}}>
                        <ul className="list-inline" style={{paddingLeft: '10px', listStyle: 'none'}}> 
                            {/*<h3 className="p-0" style={{fontSize: '1rem'}}>#{box_count} {box}-{type}</h3>*/}
                            <h3 className="p-0" style={{fontSize: '1rem'}}>#{box_count} {type}</h3>
                            {tmp_list} 
                        </ul>
                    </div>
                </Col>
            )
            box_count++;
        }
        
        return (
            <React.Fragment>

                <Row>
                    <Col className="item" md={5}>{name}</Col>
                    <Col className="qty">{this.props.cookie[box][type].qty}</Col>
                    <Col className="price">${this.props.cookie[box][type].price}</Col>
                    <Col className="total">${this.props.cookie[box][type].total}</Col>
                </Row>

                <Row className="item sub" style={{paddingTop: '10px'}}>
                    { print_subitem }
                </Row>

            </React.Fragment>
            )
    }

    render() {
        return (
            <ListGroup.Item style={{display: (this.props.cookie.total === 0 ? 'none' : null)}}>
                <h3>Cookies</h3>
                <ul className="summary-list-group">
                    <li className="summary-list header">
                        <Row>
                            <Col className="item" md={5}>Item</Col>
                            <Col className="qty">Quantity</Col>
                            <Col className="price">Price</Col>
                            <Col className="total">Total</Col>
                        </Row>
                    </li>

                    <li className="summary-list" style={{display: (this.props.cookie.variety.half.qty === 0 ? 'none' : null)}}>
                        {this.printListWithSub('variety', 'half', 'Cookie Box - Half Dozen')}
                    </li>

                    <li className="summary-list" style={{display: (this.props.cookie.variety.dozen.qty === 0 ? 'none' : null)}}>
                        {this.printListWithSub('variety', 'dozen', 'Cookie Box - Dozen')}
                    </li>

                    <li className="summary-list" style={{display: (this.props.cookie.sweet.small.qty === 0 ? 'none' : null)}}>
                        {this.printListWithSub('sweet', 'small', 'Sweets & Coffee Break')}
                    </li>

                    <li className="summary-list" style={{display: (this.props.cookie.sweet.large.qty === 0 ? 'none' : null)}}>
                        {this.printListWithSub('sweet', 'large', 'Sweets & Coffee Break')}
                    </li>

                    {/*<li className="summary-list" style={{display: (this.props.cookie.variety_half.qty === 0 ? 'none' : null)}}>
                        {this.printListWithSub('variety_half', 'half', 'Cookie Variety Box - Half Dozen')}
                    </li>

                    <li className="summary-list" style={{display: (this.props.cookie.variety_dozen.qty === 0 ? 'none' : null)}}>
                        {this.printListWithSub('variety', 'dozen', 'Cookie Variety Box - Dozen')}
                    </li>

                    <li className="summary-list" style={{display: (this.props.cookie.sweet_small.qty === 0 ? 'none' : null)}}>
                        {this.printListWithSub('sweet', 'small', 'Sweets & Coffee Break')}
                    </li>

                    <li className="summary-list" style={{display: (this.props.cookie.sweet_large.qty === 0 ? 'none' : null)}}>
                        {this.printListWithSub('sweet', 'large', 'Sweets & Coffee Break')}
                    </li>*/}

                    <li className="summary-list" style={{display: (this.props.cookie.kettle.single.qty === 0 ? 'none' : null)}}>
                        {this.printListWithSub('kettle', 'single', 'Kettle Potato Chips - 1 bag')}
                    </li>

                    <li className="summary-list" style={{display: (this.props.cookie.kettle.small.qty === 0 ? 'none' : null)}}>
                        {this.printListWithSub('kettle', 'small', 'Kettle Potato Chips - 5 bags')}
                    </li>

                    <li className="summary-list" style={{display: (this.props.cookie.kettle.large.qty === 0 ? 'none' : null)}}>
                        {this.printListWithSub('kettle', 'large', 'Kettle Potato Chips - 10 bags')}
                    </li>
                    
                    <li className="summary-list footer">
                        <Row>
                            <Col className="item" md={5}></Col>
                            <Col className="qty"></Col>
                            <Col className="price"></Col>
                            <Col className="total">${this.props.cookie.total}</Col>
                        </Row>
                    </li>
                </ul>
            </ListGroup.Item>
        )
    }
    
}

const mapStateToProps = (state) => ({
    cookie: state.cookie
});

export default connect(
    mapStateToProps
)(Cookie)