import React from 'react';
import { connect } from "react-redux";
import { Row, Col, ListGroup } from 'react-bootstrap';

const roundTo = require('roundto');

class Beverage extends React.Component {  
    
    printItem = (name, index) => {
        return (
            <li className="summary-list" style={{display: (this.props.beverage.list[name].qty === 0 ? 'none' : null)}} key={'beverage_summary_'+index}>
                <Row>
                    <Col className="item" md={5}>{this.props.beverage.list[name].name}</Col>
                    <Col className="qty">{this.props.beverage.list[name].qty}</Col>
                    <Col className="price">${this.props.beverage.list[name].price}</Col>
                    <Col className="total">${roundTo(this.props.beverage.list[name].qty*this.props.beverage.list[name].price, 2)}</Col>
                </Row>
            </li>
        )
    }

    printItems = () => {
        let print = [];
        let index = 0;
        for (let key in this.props.beverage.list){
            print.push(this.printItem(key, index))
            index++;
        }
        return print;
    }

    totalQty = () => {
        let qty = 0;
        const list = this.props.beverage.list;
        for (let key in list){
            const tmp_qty = list[key].qty;
            qty = qty + tmp_qty;
        }
        
        return qty;
    }

    render() {
        return (

            <ListGroup.Item style={{display: (this.props.beverage.total === 0 ? 'none' : null)}}>
                <h3>Beverage</h3>
                <ul className="summary-list-group">
                    <li className="summary-list header">
                        <Row>
                            <Col className="item" md={5}>Item</Col>
                            <Col className="qty">Quantity</Col>
                            <Col className="price">Price</Col>
                            <Col className="total">Total</Col>
                        </Row>
                    </li>

                    {
                        this.printItems()
                    }

                    <li className="summary-list footer">
                        <Row>
                            <Col className="item" md={5}></Col>
                            <Col className="qty">{this.totalQty()}</Col>
                            <Col className="price"></Col>
                            <Col className="total">${this.props.beverage.total}</Col>
                        </Row>
                    </li>
                </ul>
            </ListGroup.Item>
        )
    }
}

const mapStateToProps = (state) => ({
    beverage: state.beverage
});

export default connect(
    mapStateToProps
)(Beverage)