import { 
    UPDATE_CURRENT_NAV, 
    CALCULATE_SUBTOTAL,
    SUBMIT_SUCCESS,
    SUBMIT_FAILURE,
    GET_DELIVERY_METHOD,
    GET_DELIVERY_METHOD_FAILURE,
    ADDITIONAL_NOTE
} from "../actions/actionTypes";

const initialState = {
    delivery_method: null,
    loading: true,
    bagelandsheamr: 0,
    byo_breakfast: 0,
    byo_lunch: 0,
    office_party: 0,
    beverage: 0,
    subtotal: 0,
    tax: 0,
    total: 0,
    additional_comment: '',
    nav: 0,
    routes: [
        '/bagelandshmear',
        '/eggsandwich',
        '/breakfastfavorites',
        '/sweetsnack',
        '/beverage',
        '/freshsalads',
        '/cookies',
        '/lunchsandwich',
        '/summary'
    ],
    confirmation: {
        loading: true,
        success: false,
        email: null,
        orderID: null,
        error: {
            code: null
        }
    },
    error: {
        msg: ''
    }
};

export default function(state = initialState, action) {
    switch (action.type) {
        case UPDATE_CURRENT_NAV: {
            const {index} = action.payload;
            state.nav = index;
            return {...state};
        }
        case CALCULATE_SUBTOTAL: {
            const {subtotal, tax, total} = action.payload;
            state.subtotal = subtotal;
            state.tax = tax;
            state.total = total;
            return {...state};
        }
        case SUBMIT_SUCCESS: {
            const {id, email} = action.payload;
            initialState.confirmation = {
                loading: false,
                orderID: id,
                email: email,
                success: true
            }
            return {...initialState};
        }
        case SUBMIT_FAILURE: {
            const {code} = action.payload;
            state.confirmation = {
                loading: false,
                orderID: null
            }
            state.error.code = code;
            return {...state}
        }
        case GET_DELIVERY_METHOD: {
            const {delivery_method} = action.payload;
            state.delivery_method = delivery_method;
            return {...state};
        }
        case GET_DELIVERY_METHOD_FAILURE: {
            const {err} = action.payload;
            state.delivery_method = 'Failed';
            state.error.msg = err;
            return {...state}
        }
        case ADDITIONAL_NOTE: {
            const text = action.payload;
            state.additional_comment = text;
            return {...state}
        }
        default:
            return state;
    }
};