import React from 'react';
import { connect } from "react-redux";
import { Row, Col, ListGroup } from 'react-bootstrap';

class BreakfastFavorites extends React.Component {
    
    printList = (key) => {
        return (
            <li className="summary-list" style={{display: (this.props.bkfav[key].qty === 0 ? 'none' : null)}}>
                <Row>
                    <Col className="item" md={5}>{this.props.bkfav[key].name}</Col>
                    <Col className="qty">{this.props.bkfav[key].qty}</Col>
                    <Col className="price">${this.props.bkfav[key].price}</Col>
                    <Col className="total">${this.props.bkfav[key].total}</Col>
                </Row>
            </li>
        )
    }

    printBox = (box) => {
        let print = [];
        let box_count = 0;
        const list = this.props.bkfav[box].list;
        for (let box_id in list){
            let print_li = [];
            let tmp_count = 0;

            const items = ['bagel', 'whip'];

            for (let item_id in items){
                const item = items[item_id];
                print_li.push(<p className="mb-0" style={{color: '#b36a3b'}}>{item}</p>)
                for (let key in list[box_id][item]){
                    if (list[box_id][item][key] !== 0){
                        let name = this.props.bkfav[item][key].name;
                        if (item === 'whip' && key !== 'plain' && key !== 'onion' && key !== 'salmon'){
                            name = 'Reduced Fat ' + name
                        }
                        print_li.push(<li className="" key={'_child_' + tmp_count}> - { name + ' : ' + list[box_id][item][key] } </li>)
                        tmp_count++;
                    }
                }
            }

            print.push(
                <Col md={4} style={{padding: '5px'}} key={'ex_box' + box_count}>
                    <div key={box_count} className="" style={{padding: '5px', border: '1px solid #e1e1e1'}}>
                        <h3 className="p-0" style={{fontSize: '1rem'}}>{box} #{box_count+1}</h3>
                        <ul key={box_count} className="" style={{height: '', paddingLeft: '10px', listStyle: 'none'}}> {print_li} </ul>
                    </div>
                </Col>
            )
            box_count++;
        }

        return print;
    }

    printPastry = () => {
        let print_subitem = [];
        let subitem_index = 0;
        const names = this.props.bkfav.Pastry_breakfast_box.items;
        for (let key in this.props.bkfav.Pastry_breakfast_box.list){
            let name = names[key].name;
            print_subitem.push(<li className="" key={'_child_' + subitem_index}> { name + ' : ' + this.props.bkfav.Pastry_breakfast_box.list[key] } </li>)
            subitem_index++;
        }

        print_subitem = (
            <Col md={6} style={{padding: '5px', fontSize: '13px'}}>
                <div style={{padding: '5px', borderBottom: '1px solid #e1e1e1'}}>
                    {/* <h3 className="p-0" style={{fontSize: '1rem'}}></h3> */}
                    <ul className="list-inline" style={{paddingLeft: '10px', listStyle: 'none'}}> 
                        {print_subitem} 
                    </ul>
                </div>
            </Col>
        )

        return (
            <li className="summary-list" style={{display: (this.props.bkfav.Pastry_breakfast_box.qty === 0 ? 'none' : null)}}>

                <Row>
                    <Col className="item" md={5}>Pastry Breakfast Box</Col>
                    <Col className="qty">{this.props.bkfav.Pastry_breakfast_box.qty}</Col>
                    <Col className="price">${this.props.bkfav.Pastry_breakfast_box.price}</Col>
                    <Col className="total">${this.props.bkfav.Pastry_breakfast_box.total}</Col>
                </Row>

                <Row className="item sub" style={{paddingTop: '10px'}}>
                    { print_subitem }
                </Row>

            </li>
        )
    }

    render() {
        return (
            <ListGroup.Item style={{display: (this.props.bkfav.total === 0 ? 'none' : null)}}>
                <h3>Breakfast Favorites</h3>
                <ul className="summary-list-group">
                    <li className="summary-list header">
                        <Row>
                            <Col className="item" md={5}>Item</Col>
                            <Col className="qty">Quantity</Col>
                            <Col className="price">Price</Col>
                            <Col className="total">Total</Col>
                        </Row>
                    </li>
                    
                    <li className="summary-list" style={{display: (this.props.bkfav.for_group.qty === 0 ? 'none' : null)}}>
                        <Row>
                            <Col className="item" md={5}>{this.props.bkfav.for_group.name}</Col>
                            <Col className="qty">{this.props.bkfav.for_group.qty}</Col>
                            <Col className="price">${this.props.bkfav.for_group.price}</Col>
                            <Col className="total">${this.props.bkfav.for_group.total}</Col>
                        </Row>

                        <Row className="item sub" style={{paddingTop: '10px'}}>
                            { this.printBox('for_group') }
                        </Row>
                    </li>

                    <li className="summary-list" style={{display: (this.props.bkfav.salmon_platter.qty === 0 ? 'none' : null)}}>
                        <Row>
                            <Col className="item" md={5}>{this.props.bkfav.salmon_platter.name}</Col>
                            <Col className="qty">{this.props.bkfav.salmon_platter.qty}</Col>
                            <Col className="price">${this.props.bkfav.salmon_platter.price}</Col>
                            <Col className="total">${this.props.bkfav.salmon_platter.total}</Col>
                        </Row>

                        <Row className="item sub" style={{paddingTop: '10px'}}>
                            { this.printBox('salmon_platter') }
                        </Row>
                    </li>

                    <li className="summary-list" style={{display: (this.props.bkfav.nosh_box.qty === 0 ? 'none' : null)}}>
                        <Row>
                            <Col className="item" md={5}>{this.props.bkfav.nosh_box.name}</Col>
                            <Col className="qty">{this.props.bkfav.nosh_box.qty}</Col>
                            <Col className="price">${this.props.bkfav.nosh_box.price}</Col>
                            <Col className="total">${this.props.bkfav.nosh_box.total}</Col>
                        </Row>

                        <Row className="item sub" style={{paddingTop: '10px'}}>
                            { this.printBox('nosh_box') }
                        </Row>
                    </li>

                    {/*<li className="summary-list" style={{display: (this.props.bkfav.nosh_box.qty === 0 ? 'none' : null)}}>
                        <Row>
                            <Col className="item" md={5}>{this.props.bkfav.nosh_box.name}</Col>
                            <Col className="qty">{this.props.bkfav.nosh_box.qty}</Col>
                            <Col className="price">${this.props.bkfav.nosh_box.price}</Col>
                            <Col className="total">${this.props.bkfav.nosh_box.total}</Col>
                        </Row>

                        <Row className="item sub" style={{paddingTop: '10px'}}>
                            { this.printBox('nosh_box') }
                        </Row>
                    </li>*/}

                    <li className="summary-list" style={{display: (this.props.bkfav.bagel_shmear_break_box.qty === 0 ? 'none' : null)}}>
                        <Row>
                            <Col className="item" md={5}>{this.props.bkfav.bagel_shmear_break_box.name}</Col>
                            <Col className="qty">{this.props.bkfav.bagel_shmear_break_box.qty}</Col>
                            <Col className="price">${this.props.bkfav.bagel_shmear_break_box.price}</Col>
                            <Col className="total">${this.props.bkfav.bagel_shmear_break_box.total}</Col>
                        </Row>

                        <Row className="item sub" style={{paddingTop: '10px'}}>
                            { this.printBox('bagel_shmear_break_box') }
                        </Row>
                    </li>
                    
                    {/* { this.printList('bagel_shmear_break_box') } */}
                    { this.printList('power_protein_breakfast_box') }
                    { this.printPastry() }

                    <li className="summary-list footer">
                        <Row>
                            <Col className="item" md={5}></Col>
                            <Col className="qty"></Col>
                            <Col className="price"></Col>
                            <Col className="total">${this.props.bkfav.total}</Col>
                        </Row>
                    </li>
                </ul>
            </ListGroup.Item>
        )
    }
}

const mapStateToProps = (state) => ({
    bkfav: state.bk_fav_reducer
});

export default connect(
    mapStateToProps
)(BreakfastFavorites)