import React from 'react';
import { connect } from "react-redux";

import { Row, Col, ButtonToolbar, Button, ListGroup, Modal } from 'react-bootstrap';
// import NumberInput from '../common/numberInput.js';

import { addPastry, closePastry, updatePastry } from '../../redux/actions/bk_fav_action';

class Pastry extends React.Component {
    constructor(props) {
        super(props);

        let selected = null;

        // if (this.props.mode === 'edit'){ 
        //     selected = this.props.fresh_salads[this.props.box].list[this.props.id]; 
        // }

        this.state = {
            selected: selected,
            error: false,
            error_msg: ''
        }
        
    }

    closePastry = () => {
        this.props.closePastry();
    }

    addPastry = () => {
        if (this.state.selected === null){
            this.displayErr();
        }
        else {
            this.props.addPastry(this.state.selected);
        }
    }

    updatePastry = () => {
        if (this.state.selected === null){
            this.displayErr();
        }
        else {
            this.props.updatePastry(this.state.selected);
        }
    }

    displayErr = () => {
        this.setState({
            error: true
        })
    }

    onSelectItem = (key) => {
        this.setState({
            selected: key
        });
    }

    printItems = () => {
        let print = [];
        let index = 0;
        for (let key in this.props.favorites[this.props.box].items){
            const name = this.props.favorites[this.props.box].items[key].name;
            print.push(
                <Button onClick={this.onSelectItem.bind(this, key)} className={(this.state.selected === key ? 'btn-select' : 'btn-unselect') + ' mr-1 mb-1'} key={'item_'+index}>
                    {name}
                </Button>
            )
            index++;
        }

        return (
                <ButtonToolbar>
                    {print}
                </ButtonToolbar>
            );
    }

    render() {
        return(
            <Modal show={true}>
                <Modal.Header style={{fontWeight: '600'}}>
                    Choose Muffin or Strudel
                </Modal.Header>

                <Modal.Body style={{padding: '5px'}}>
                    <ListGroup >

                        <ListGroup.Item className="menu-list es-list-group-item" style={{paddingTop: '0px', marginTop: '0px', borderWidth: '0px'}}>

                            {/* <Row style={{marginTop: '25px'}}>
                                <Col className="wrap_name">
                                    <p className="item-header" style={{marginBottom: '0px', paddingLeft: '0px', color: '#b36a3b'}}>Cookie</p>
                                </Col>
                            </Row> */}

                            <Row className="mt-3 mb-3">
                                <Col md={12}>
                                    {
                                        this.printItems()
                                    }
                                </Col>
                            </Row>

                        </ListGroup.Item>

                    </ListGroup>
                </Modal.Body>

                <Modal.Footer style={{justifyContent: 'left'}}>
                    <ButtonToolbar className="">
                        {
                            this.props.mode === 'add' ?
                            <Button variant="primary" onClick={this.addPastry.bind(this)} className="add-btn">
                                Add
                            </Button>
                            :
                            <Button variant="primary" onClick={this.updatePastry.bind(this)} className="add-btn">
                                Update
                            </Button>
                        }
                        <Button variant="light" onClick={this.closePastry.bind(this)} className="cancel-btn ml-2">
                            Cancel
                        </Button>
                    </ButtonToolbar>
                </Modal.Footer>

            </Modal>
        )
    }
}

const mapStateToProps = (state) => ({
    favorites: state.bk_fav_reducer
});

const mapDispatchToProps = dispatch => (
    {
        addPastry: (item) => dispatch(addPastry(item)),
        closePastry: () => dispatch(closePastry()),
        updatePastry: (item) => dispatch(updatePastry(item))
    }
)

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Pastry)