import React from 'react';
import { connect } from "react-redux";
// import { Link } from 'react-router-dom';
// import { updateCurrentNav, calculateSubtotal, submitOrder } from '../redux/actions/main';
import { Card, Spinner } from 'react-bootstrap';

// import TopNav from './common/TopNav.js';

class Confirmation extends React.Component {

    // constructor(props) {
    //     super(props);
    // }

    render() {
        return (
            <React.Fragment>
            {
                this.props.state.main.confirmation.loading ?
                <div style={{textAlign: 'center', padding: '100px 0px'}}>
                    <Spinner animation="grow" style={{backgroundColor: '#f58220'}} />
                </div>
                :
                <Card className="">

                    {
                        this.props.state.main.confirmation.orderID === null ?
                        <React.Fragment>
                            <Card.Body>
                                <Card.Text>
                                    We're sorry. An error occurred while processing your order. Please try again later.
                                </Card.Text>
                            </Card.Body>

                            <div className="card-body">
                                        
                                <div className="row">
                                    <div className="col-md-4">
                                        <a href="/catering/online/agreement.php?r=einsteins" className="btn add-btn btn-lg btn-block" style={{width: '100%'}}>Try Again</a>
                                    </div>
                                </div>

                            </div>
                        </React.Fragment>
                        :
                        <React.Fragment>
                            <Card.Body>
                                <Card.Text>
                                    <span style={{fontWeight: '500', lineHeight: '2.2', fontSize: '1.7rem'}}>Thank You</span><br style={{lineHeight: '2.2'}} />
                                    Your order is confirmed. For your convenience, an email has been sent to {this.props.state.main.confirmation.email}. <br />
                                    Your Order ID is {this.props.state.main.confirmation.orderID}.
                                </Card.Text>
                            </Card.Body>

                            <div className="card-body">
                                        
                                <div className="row">
                                    <div className="col-md-4">
                                        <a href="/catering/online/agreement.php?r=einsteins" className="btn add-btn btn-lg btn-block" style={{width: '100%'}}>Order Again</a>
                                    </div>

                                    <div className="col-md-4">
                                        <a href="/dining/sumc/einsteins" className="btn add-btn btn-lg btn-block" style={{width: '100%'}}>Go to Einsteins Cafe</a>
                                    </div>

                                    <div className="col-md-4">
                                        <a href="/" className="btn add-btn btn-lg btn-block" style={{width: '100%'}}>Go to Union</a>
                                    </div>
                                </div>

                            </div>
                        </React.Fragment>
                    }

                </Card>
            }
            </React.Fragment>
        )
    }
}

const mapStateToProps = (state) => ({
    state: state
});

const mapDispatchToProps = dispatch => (
    {
    }
)

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Confirmation)