import { 
    UPDATE_CURRENT_NAV, 
    SUBMIT_ORDER, 
    CALCULATE_SUBTOTAL,
    SUBMIT_SUCCESS,
    SUBMIT_FAILURE,
    GET_DELIVERY_METHOD,
    GET_DELIVERY_METHOD_FAILURE,
    ADDITIONAL_NOTE
} from './actionTypes.js'

export const updateCurrentNav = (index) => ({
    type: UPDATE_CURRENT_NAV,
    payload: {
        index
    }
})

export const calculateSubtotal = (subtotal, tax, total) => ({
    type: CALCULATE_SUBTOTAL,
    payload: {
        subtotal, tax, total
    }
})

export const initSubmitOrder = () => ({
    type: SUBMIT_ORDER
})

export const formValidationFailure = () => ({

})

export const formSubmitSuccess = (id, email) => ({
    type: SUBMIT_SUCCESS,
    payload: {
        id, email
    }
})

export const formSubmitFailure = (code) => ({
    type: SUBMIT_FAILURE,
    payload: {
        code
    }
})

export function submitOrder(data) {
    return (dispatch) => {
        dispatch(initSubmitOrder());

        // Validate Form

        let error = false;

        if (error){
            dispatch(formValidationFailure());
        }
        else {
            // console.log(data);
            const fetch_option = {
                method: 'POST',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(data)
            }

            // const url = 'http://192.168.99.100/api/catering/retail/catalyst/submit';
            const url = '/api/catering/retail/einsteins/submit';

            fetch(url, fetch_option)
            .then((res) => {
                // console.log(res);
                if(res.ok) {
                    return res.json();
                } else {
                    // console.log(res);
                    throw new Error('An error occurred.');
                }
            })
            .then(res => {
                // console.log('return', res.return);
                if (res.success){
                    dispatch(formSubmitSuccess(res.return.order_id, res.return.email));
                }
                else {
                    dispatch(formSubmitFailure(res.return.code));
                }
            })
            .catch(err => {
                console.log(err);
                // dispatch(formSubmitFailure(err));
            })
        }
    }
}

export function getDeliveryMethod() {
    return (dispatch) => {
        const fetch_option = {
            method: 'POST',
            headers: {
                'Accept': 'application/json'
            }
        }

        // const url = 'http://192.168.99.100/catering/retail/customer_info/getDeliveryMethod';
        const url = '/api/catering/retail/customer_info/getDeliveryMethod';

        fetch(url, fetch_option)
        .then((res) => {
            if(res.ok) {
                return res.json();
            } else {
                // console.log(res);
                throw new Error('An error occurred.');
            }
        })
        .then(res => {
            // console.log('return', res.return);
            dispatch(getDeliveryMethodSuccess(res.return.delivery_method));
        })
        .catch(err => {
            console.log(err);
            dispatch(getDeliveryMethodFailure(err));
        })
    }
}

export const getDeliveryMethodSuccess = (delivery_method) => ({
    type: GET_DELIVERY_METHOD,
    payload: {
        delivery_method
    }
})

export const getDeliveryMethodFailure = (err) => ({
    type: GET_DELIVERY_METHOD_FAILURE,
    payload: {
        err
    }
})

export const handleTextarea = (text) => ({
    type: ADDITIONAL_NOTE,
    payload: text
})