import React from "react";
import { connect } from "react-redux";

import { Row, Col, Card, ListGroup, ButtonToolbar, Button } from 'react-bootstrap';
import NumberInput from '../common/numberInput.js';

import { addBox, closeBox, updateBox } from '../../redux/actions/bagelandsheamr_action';

class Choice extends React.Component {
    constructor(props) {
        super(props);

        let list = {
            bagel: {},
            whip: {}
        };
        if (this.props.bagel_shmear.mode === 'add'){ 
            for (let key in this.props.bagel_shmear.bagel){ list.bagel[key] = 0; }
            for (let key in this.props.bagel_shmear.whip){ list.whip[key] = 0; }
        }
        else {
            list = this.props.bagel_shmear[this.props.bagel_shmear.box].list[this.props.bagel_shmear.id];
        }
        
        this.state = {
            
        

            list: list,
            bagel_qty: (this.props.bagel_shmear.mode === 'edit' ? (this.props.box === 'baker_dozen' ? 13 : (this.props.box === 'half_dozen' ? 6 : 24)) : 0),
            bagel_err: false,
            bagel_max: (this.props.box === 'baker_dozen' ? 13 : (this.props.box === 'half_dozen' ? 6 : (this.props.box === 'nosh_box' ? 24 : 0))),
            whip_qty: (this.props.bagel_shmear.mode === 'edit' ? (this.props.box === 'baker_dozen' ? 2 : (this.props.box === 'nosh_box' ? 4 : 1)) : 0),
            whip_err: false,
            whip_max: (this.props.box === 'baker_dozen' ? 2 : (this.props.box === 'nosh_box' ? 4 : 1)),


            sandwich_qty: 0,
            cookie_qty: 0,
            chip_qty: 0,
            drink_qty: 0,
            error: false,
            sandwich_err: false,
            cookie_err: false,
            chip_err: false,
            drink_err: false,
            error_msg: '',
            // max_bagel: (this.props.box === 'baker_dozen' ? 13 : 24),
            // max_whip: (this.props.box === 'baker_dozen' ? 2 : 4)
        }
    }

    checkQty = (type, name, qty) => {
        let tmp_qty = 0;
        for (let key in this.state.list[type]){
            if (key !== name){
                tmp_qty = tmp_qty + this.state.list[type][key];
            }
            else {
                tmp_qty = tmp_qty + qty
            }
        }

        return tmp_qty;
    }

    closeBox = () => {
        this.props.closeBox();
    }

    addBox = () => {
        if (this.validateBox()){

        }
        else {
            this.props.addBox(this.state.list);
        }
    }

    updateBox = () => {
        if (this.validateBox()){

        }
        else {
            this.props.updateBox(this.state.list);
        }
    }

    validateBox = () => {
        let error = false;       

        // Check Bagel 
        if (this.state.bagel_qty !== this.state.bagel_max){
            this.setState({bagel_err: true});
            error = true;
        }

        // Check Whip
        if (this.state.whip_qty !== this.state.whip_max){
            this.setState({whip_err: true});
            error = true;
        }

        return error;
    }

    printBagels = () => {
        let print = [];
        let index = 0;
        const items = this.props.bagel_shmear.bagel;
        
        for (let key in items) {
            print.push(
                <Col md={6} className="wrap_qty mb-3" key={'bagel_input'+index}>
                    {items[key].name}
                    <NumberInput 
                        readonly={true}
                        onChange={this.onChangeBagel.bind(this)}
                        name={key}
                        value={this.state.list.bagel[key]}
                        style={{float: 'left', textAlign: 'left'}}
                    />
                </Col>
            )
            index++;
        }

        return print;
    }

    onChangeBagel = (name, qty) => {
        const checkQty = this.checkQty('bagel', name, qty)
        if (checkQty < (this.state.bagel_max + 1)){
            this.setState({
                list: {
                    ...this.state.list,
                    bagel: {
                        ...this.state.list.bagel,
                        [name]: qty
                    }
                },
                bagel_qty: checkQty
            })
        }
        else {
            this.setState({
                bagel_err: true
            }, () => {
                setTimeout(() => {
                    this.setState({
                        bagel_err: false
                    })
                }, 1000)
            })
            return 'error';
        }
    }

    printWhip = (key) => {
        return (
            <Col md={6} className="wrap_qty mb-3" >
                {this.props.bagel_shmear.whip[key].name}
                <NumberInput 
                    readonly={true}
                    onChange={this.onChangeWhip.bind(this)}
                    name={key}
                    value={this.state.list.whip[key]}
                    style={{float: 'left', textAlign: 'left'}}
                />
            </Col>
        );
    }

    onChangeWhip = (name, qty) => {
        const checkQty = this.checkQty('whip', name, qty)
        if (checkQty < this.state.whip_max + 1){
            this.setState({
                list: {
                    ...this.state.list,
                    whip: {
                        ...this.state.list.whip,
                        [name]: qty
                    }
                },
                whip_qty: checkQty
            })
        }
        else {
            this.setState({
                whip_err: true
            }, () => {
                setTimeout(() => {
                    this.setState({
                        whip_err: false
                    })
                }, 1000)
            })
            return 'error';
        }
    }

    render() {
        if (this.props.box === 'whip'){
            return (
            <Card className="col-md-12 p-0">
                <Card.Body className={this.state.whip_err ? 'su-alert' : ''}>
                    <Card.Text>You selected <strong>{this.state.whip_qty} of {this.state.whip_max}</strong> Whips.</Card.Text>
                </Card.Body>
                <ListGroup style={{marginTop: '10px'}}>
                    <ListGroup.Item className="menu-list">
                        <Row style={{marginTop: '5px'}}>
                            <Col className="wrap_name">
                                <p className="item-header" style={{marginBottom: '10px', paddingLeft: '0px', fontSize: '25px', color: '#b36a3b'}}>Double-Whipped Shmear</p>
                                <p className="item-description"></p>
                            </Col>
                        </Row>
                        <Row>
                            {this.printWhip('plain')}
                            {this.printWhip('onion')}
                            {this.printWhip('salmon')}
                        </Row>
                        <Row style={{marginTop: '5px'}}>
                            <Col className="wrap_name">
                                <p className="item-header" style={{marginBottom: '10px', paddingLeft: '0px', fontSize: '15px', color: '#b36a3b'}}>25% Reduced Fat</p>
                                <p className="item-description"></p>
                            </Col>
                        </Row>
                        <Row>
                            {this.printWhip('plainRF')}
                            {this.printWhip('garden')}
                            {this.printWhip('honey')}
                            {this.printWhip('jalapeno')}
                            {this.printWhip('strawberry')}
                        </Row>
                    </ListGroup.Item>
                </ListGroup>
                <Row>
                    <Col>
                        <ButtonToolbar className="mt-3">
                            {
                                this.props.bagel_shmear.mode === 'add' ?
                                <Button variant="primary" size="md" onClick={this.addBox.bind(this)} className="add-btn">
                                    Add
                                </Button>
                                :
                                <Button variant="primary" size="md" onClick={this.updateBox.bind(this)} className="add-btn">
                                    Update
                                </Button>
                            }
                            <Button variant="light" size="md" onClick={this.closeBox.bind(this)} className="cancel-btn ml-2">
                                Cancel
                            </Button>
                        </ButtonToolbar>
                    </Col>
                </Row>
            </Card>
            )
        } else {
        return (
            <Card className="col-md-12 p-0">

                <Card.Header>
                    <p>{this.props.bagel_shmear[this.props.box].name}</p>
                    <p>Serves up to {(this.props.box === 'baker_dozen' ? 13 : (this.props.box === 'half_dozen' ? 6 : 24))}</p>
                    <ul>
                        <li>Choose your bagels</li>
                        <li>Choose your whipped shmear</li>
                    </ul>
                </Card.Header>

                <Card.Body className={this.state.bagel_err ? 'su-alert' : ''}>
                    <Card.Text>You selected <strong>{this.state.bagel_qty} of {this.state.bagel_max}</strong> Bagels.</Card.Text>
                </Card.Body>

                <ListGroup style={{marginTop: '10px'}}>

                    <ListGroup.Item className="menu-list">

                        <Row style={{marginTop: '5px'}}>
                            <Col className="wrap_name">
                                <p className="item-header" style={{marginBottom: '10px', paddingLeft: '0px', fontSize: '25px', color: '#b36a3b'}}>Fresh-Baked Bagels</p>
                                <p className="item-description"></p>
                            </Col>
                        </Row>
                        
                        <Row>
                            {this.printBagels()}
                        </Row>

                    </ListGroup.Item>

                </ListGroup>

                <Card.Body className={this.state.whip_err ? 'su-alert' : ''}>
                    <Card.Text>You selected <strong>{this.state.whip_qty} of {this.state.whip_max}</strong> Whips.</Card.Text>
                </Card.Body>

                <ListGroup style={{marginTop: '10px'}}>

                    <ListGroup.Item className="menu-list">

                        <Row style={{marginTop: '5px'}}>
                            <Col className="wrap_name">
                                <p className="item-header" style={{marginBottom: '10px', paddingLeft: '0px', fontSize: '25px', color: '#b36a3b'}}>Double-Whipped Shmear</p>
                                <p className="item-description"></p>
                            </Col>
                        </Row>
                        
                        <Row>
                            {this.printWhip('plain')}
                            {this.printWhip('onion')}
                            {this.printWhip('salmon')}
                        </Row>

                        <Row style={{marginTop: '5px'}}>
                            <Col className="wrap_name">
                                <p className="item-header" style={{marginBottom: '10px', paddingLeft: '0px', fontSize: '15px', color: '#b36a3b'}}>25% Reduced Fat</p>
                                <p className="item-description"></p>
                            </Col>
                        </Row>
                        
                        <Row>
                            {this.printWhip('plainRF')}
                            {this.printWhip('garden')}
                            {this.printWhip('honey')}
                            {this.printWhip('jalapeno')}
                            {this.printWhip('strawberry')}
                        </Row>

                    </ListGroup.Item>

                </ListGroup>

                

                <Row>
                    <Col>
                        <ButtonToolbar className="mt-3">
                            {
                                this.props.bagel_shmear.mode === 'add' ?
                                <Button variant="primary" size="md" onClick={this.addBox.bind(this)} className="add-btn">
                                    Add
                                </Button>
                                :
                                <Button variant="primary" size="md" onClick={this.updateBox.bind(this)} className="add-btn">
                                    Update
                                </Button>
                            }
                            <Button variant="light" size="md" onClick={this.closeBox.bind(this)} className="cancel-btn ml-2">
                                Cancel
                            </Button>
                        </ButtonToolbar>
                    </Col>
                </Row>

            </Card>
        )
    }
    }
}

const mapStateToProps = (state) => ({
    bagel_shmear: state.bagelandsheamr
});

const mapDispatchToProps = dispatch => (
    {
        addBox: (list) => dispatch(addBox(list)),
        closeBox: () => dispatch(closeBox()),
        updateBox: (id, list) => dispatch(updateBox(id, list))
    }
)

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Choice)