import {
    UPDATE_FRESHSALADS_QTY,
    OPEN_FRESHSALADS_BOX,
    OPEN_EDIT_FRESHSALADS_BOX,
    CLOSE_FRESHSALADS_BOX,
    ADD_FRESHSALADS_BOX,
    UPDATE_FRESHSALADS_BOX,
    REMOVE_FRESHSALADS_BOX,
    REMOVE_FRESHSALADS_CHOICE
} from './actionTypes.js'

export const updateQty = (name, qty) => ({
    type: UPDATE_FRESHSALADS_QTY,
    payload: {
        name,
        qty
    }
})

export const openBox = (box, mode) => ({
    type: OPEN_FRESHSALADS_BOX,
    payload: {
        box,
        mode
    }
})

export const openEditBox = (box, id) => ({
    type: OPEN_EDIT_FRESHSALADS_BOX,
    payload: {
        box,
        id
    }
})

export const closeBox = () => ({
    type: CLOSE_FRESHSALADS_BOX
})

export const addBox = (item) => ({
    type: ADD_FRESHSALADS_BOX,
    payload: {
        item
    }
})

export const updateBox = (item) => ({
    type: UPDATE_FRESHSALADS_BOX,
    payload: {
        item
    }
})

export const removeBox = (box, id) => ({
    type: REMOVE_FRESHSALADS_BOX,
    payload: {
        box,
        id
    }
})

export const removeChoice = (type, key) => ({
    type: REMOVE_FRESHSALADS_CHOICE,
    payload: {
        type,
        key
    }
})