import React from 'react';
import { connect } from "react-redux";
import { Link } from 'react-router-dom';
import { Row, Col, ButtonToolbar } from 'react-bootstrap';

import { updateCurrentNav } from '../redux/actions/main';

const roundTo = require('roundto');

class Bottom extends React.Component {

    getTotal = () => {
        return roundTo(
                this.props.global.bagelandsheamr.total +
                this.props.global.egg_sandwich.total +
                this.props.global.bk_fav_reducer.total +
                this.props.global.sweet_snack.total + 
                this.props.global.beverage.total + 
                this.props.global.fresh_salads.total + 
                this.props.global.cookie.total +
                this.props.global.lunchsandwich.total
                , 2
            );
    }

    render() {

        const navIndex = this.props.global.main.nav;
        const routes = this.props.global.main.routes;

        return(
            <React.Fragment>
                <Row>
                    <Col>
                        <p style={{fontSize: '22px', fontWeight: '600', textAlign: 'right', padding: '20px 15px'}}>Subtotal ${this.getTotal()}</p>
                    </Col>
                </Row>
                <ButtonToolbar>

                    {
                        (navIndex !== 0 && navIndex !== 8) ?
                        <Link
                        className={"btn add-btn"}
                        style={{marginRight: '10px', width: '200px'}}
                        to={routes[navIndex-1]}
                        >Prev</Link>
                        :
                        null
                    }

                    {
                        navIndex !== 8 ? 
                        <Link
                        className={"btn add-btn"}
                        style={{marginLeft: 'auto', width:'200px'}}
                        to={routes[navIndex+1]}
                        >Next</Link>
                        :
                        <Link
                        className={"btn add-btn"}
                        style={{marginLeft: 'auto', width:'150px'}}
                        to={routes[0]}
                        >Add More Food</Link>
                    }

                </ButtonToolbar>

                {/* <ButtonToolbar style={{marginTop: '15px'}}>
                    {
                        navIndex !== 5 ?
                        <Link
                        className={"btn add-btn"}
                        style={{width:'100%'}}
                        to={routes[5]}
                        >Review Order</Link>
                        :
                        null
                    }
                </ButtonToolbar> */}
            </React.Fragment>
        )
    }
}

const mapStateToProps = (state) => ({
    global: state
});

const mapDispatchToProps = dispatch => (
    {
        updateCurrentNav: (index) => dispatch(updateCurrentNav(index))
    }
)

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Bottom)