import React from "react";
import { connect } from "react-redux";

import { Row, Col, Card, ListGroup, ButtonToolbar, Button } from 'react-bootstrap';
import NumberInput from '../common/numberInput.js';
import TopNav from '../common/TopNav.js';

import { updateCurrentNav } from '../../redux/actions/main';
import { updateQty, openBox, openEditBox, removeBox, removePastry } from '../../redux/actions/bk_fav_action';

// import IndividualBreakfastBoxes from './IndividualBreakfastBoxes';
import Bottom from '../bottom';

import Bagel from './Bagel';
import Pastry from './Pastry';

import styles from '../common/style.js';

// const subHeader = {
//     marginTop: '1rem',
//     marginBottom: '10px',
//     paddingLeft: '20px',
//     fontWeight: '600',
//     fontSize: '17px'
// }
// const style_listGroup = {
//     padding: '0px',
//     paddingLeft: '20px',
//     borderBottom: '1px solid #dadada'
// } 

class BreakfastFavorites extends React.Component {
    constructor(props) {
        super(props);
        
        this.state = {
            total: this.props.favorites.total
        }

        this.props.updateCurrentNav(2);
    }

    onChangeHandler = (name, value) => {
        this.props.updateQty(name, value);
    }

    openBox = (name) => {
        this.props.openBox(name, 'add');
    }

    openEditBox = (box, id) => {
        this.props.openEditBox(box, id);
    }

    removeBox = (box, id) => {
        this.props.removeBox(box, id);
    }

    printItem = (key) => {

        return (
            <React.Fragment>

                <Row style={{marginTop: '10px'}}>
                    <Col className="wrap_name">
                        <p className="item-header" style={{marginBottom: '0px', paddingLeft: '0px', color: '#b36a3b'}}>{this.props.favorites[key].name}</p>
                        <p className="item-description">{this.props.favorites[key].description}</p>
                    </Col>
                </Row>

                <Row>
                    <Col className="wrap_qty">
                        {/* <p style={{float: 'left', fontWeight: '600'}}>Serves up to {serves}</p> */}
                        <NumberInput 
                        readonly={true}
                        onChange={this.onChangeHandler.bind(this)}
                        name={key}
                        value={this.props.favorites[key].qty}
                    />
                        <p style={{float: 'right', fontWeight: '600'}}>${this.props.favorites[key].price}</p> 
                    </Col>
                </Row>

            </React.Fragment>
        )
    }
    
    printItemWithBox = (key) => {
        let serves = null;
        if (key === 'for_group'){serves = 20}
        else if (key === 'salmon_platter'){serves = 13}
        else if (key === 'nosh_box'){serves = 21}

        return (
            <React.Fragment>

                <Row style={{marginTop: '10px'}}>
                    <Col className="wrap_name">
                        <p className="item-header" style={{marginBottom: '0px', paddingLeft: '0px', color: '#b36a3b'}}>{this.props.favorites[key].name}</p>
                        <p className="item-description">{this.props.favorites[key].description}</p>
                    </Col>
                </Row>

                <Row>
                    <Col className="wrap_qty">
                        {  
                            key === 'bagel_shmear_break_box' || key === 'Pastry_breakfast_box' ?
                            null :
                            <p style={{float: 'left', fontWeight: '600'}}>Serves up to {serves}</p>
                        }
                        <NumberInput 
                            noleft={true}
                            readonly={true}
                            onChange={this.openBox.bind(this)}
                            name={key}
                            value={this.props.favorites[key].qty}
                        />
                        <p style={{float: 'right', fontWeight: '600'}}>${this.props.favorites[key].price}</p> 
                    </Col>
                </Row>

                {
                    this.props.favorites[key].qty === 0 ?
                    null
                    :
                    <React.Fragment>
                    {
                        key === 'Pastry_breakfast_box' ?
                        <Row style={{marginTop: '1rem', marginBottom: '1rem'}}>
                            { this.printPastry() }
                        </Row>
                        :
                        <Row style={{marginTop: '1rem', marginBottom: '1rem', borderBottom: '1px solid #e1e1e1'}}>
                            { this.printSelectedBox(key) }
                        </Row>
                    }
                    </React.Fragment>
                }

            </React.Fragment>
        )

    }

    printSelectedBox = (box) => {
        let print = [];
        let box_count = 0;
        const list = this.props.favorites[box].list;
        for (let box_id in list){
            let print_li = [];
            let tmp_count = 0;

            const items = ['bagel', 'whip'];

            for (let item_id in items){
                const item = items[item_id];
                print_li.push(<p className="mb-0" style={{color: '#b36a3b'}} key={box+'_'+item_id}>{item}</p>)
                for (let key in list[box_id][item]){
                    if (list[box_id][item][key] !== 0){
                        let name = this.props.favorites[item][key].name;
                        if (item === 'whip' && key !== 'plain' && key !== 'onion' && key !== 'salmon'){
                            name = 'Reduced Fat ' + name
                        }
                        print_li.push(<li className="" key={'_child_' + tmp_count}> - { name + ' : ' + list[box_id][item][key] } </li>)
                        tmp_count++;
                    }
                }
            }

            print.push(
                <Col md={4} className="selected_box_wrap" style={{padding: '5px'}} key={'ex_box' + box_count}>
                    <div key={box_count} className="" style={{padding: '5px', border: '1px solid #e1e1e1'}}>
                        <h3 className="p-0" style={{fontSize: '1rem'}}>{box} #{box_count+1}
                            <ButtonToolbar style={{float: 'right'}}>
                                <Button onClick={this.openEditBox.bind(this, box, box_count)} className="edit-btn">Edit</Button>
                                <Button onClick={this.removeBox.bind(this, box, box_count)} className="remove-btn">Remove</Button>
                            </ButtonToolbar>
                        </h3>
                        <ul key={box_count} className="" style={{height: '', paddingLeft: '10px', listStyle: 'none'}}> {print_li} </ul>
                    </div>
                </Col>
            )
            box_count++;
        }

        return print;
    }

    printPastry = () => {
        let printPastry = [];
        let count = 0;
        const list = this.props.favorites.Pastry_breakfast_box.list;
        
        for (let key in list){

            let name = this.props.favorites.Pastry_breakfast_box.items[key].name;
            printPastry.push(
                <Col md={12} key={name+'_selected_item_'+count} className="mb-1">
                    <NumberInput 
                        noright={true}
                        readonly={true}
                        onChange={this.removePastry.bind(this, key)}
                        name={'pastry_'+key}
                        value={list[key]}
                    />
                    <p className="mb-0" style={{float: 'right'}}>- {name}</p>
                </Col>
            )
            count++;

        }

        return printPastry;
    }

    removePastry = (item) => {
        this.props.removePastry(item)
    }

    render() {
        if (this.props.favorites.box !== null && this.props.favorites.box !== 'Pastry_breakfast_box'){
            return <Bagel box={this.props.favorites.box} />
        }
        else {
            return(
                <Card className="col-md-12 p-0 menu-card">

                    <Card.Header>
                        <TopNav />
                    </Card.Header>

                    <Card.Body>
                        <Card.Title>Breakfast Favorites</Card.Title>
                    </Card.Body>

                    <ListGroup>

                        <ListGroup.Item className="menu-list es-list-group-item" style={styles.listGroupItem}>

                            {this.printItemWithBox('for_group')}

                        </ListGroup.Item>

                        <ListGroup.Item className="menu-list es-list-group-item" style={styles.listGroupItem}>

                            {this.printItemWithBox('salmon_platter')}

                        </ListGroup.Item>

                        <ListGroup.Item className="menu-list es-list-group-item" style={styles.listGroupItem}>

                            {this.printItemWithBox('nosh_box')}

                        </ListGroup.Item>

                    </ListGroup>


                    <div className="sub-header col-6">
                        <p>Individual Breakfast Boxes</p>
                        <span>Perfect for groups on the go, served with utensils</span>
                    </div>

                    <ListGroup>

                        <ListGroup.Item className="menu-list es-list-group-item" style={styles.listGroupItem}>

                            {this.printItemWithBox('bagel_shmear_break_box')}

                        </ListGroup.Item>

                        <ListGroup.Item className="menu-list es-list-group-item" style={styles.listGroupItem}>

                            {this.printItem('power_protein_breakfast_box')}

                        </ListGroup.Item>

                        <ListGroup.Item className="menu-list es-list-group-item" style={styles.listGroupItem}>

                            {this.printItemWithBox('Pastry_breakfast_box')}

                            {
                                this.props.favorites.box === 'Pastry_breakfast_box' ?
                                <Pastry box={this.props.favorites.box} mode='add' />
                                :
                                null
                            }

                        </ListGroup.Item>

                        <ListGroup.Item className="menu-list es-list-group-item total">
                            <Row>
                                <Col>
                                    ${this.props.favorites.total}
                                </Col>
                            </Row>
                        </ListGroup.Item>

                    </ListGroup>
                    

                    <Row>
                        <Col md={12}>
                            <Bottom />
                        </Col>
                    </Row>

                </Card>
            )
        }


    }
}

const mapStateToProps = (state) => ({
    favorites: state.bk_fav_reducer
});

const mapDispatchToProps = dispatch => (
    {
        updateQty: (name, qty) => dispatch(updateQty(name, qty)),
        openBox: (name, mode) => dispatch(openBox(name, mode)),
        openEditBox: (box, id) => dispatch(openEditBox(box, id)),
        removeBox: (box, key) => dispatch(removeBox(box, key)),
        removePastry: (item) => dispatch(removePastry(item)),
        updateCurrentNav: (index) => dispatch(updateCurrentNav(index))
    }
)

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(BreakfastFavorites)