import React from 'react';

class NumberInput extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            value: parseInt(this.props.value)
        }
    }

    increase = () => {
        this.setState({
            value: this.state.value + 1
        }, function() {
            const result = this.props.onChange(this.props.name, this.state.value);
            if (result === 'error'){
                this.setState({
                    value: this.state.value - 1
                })
            }
        })
    }

    decrease = () => {
        if (this.state.value-1 >= 0) {
            this.setState({
                value: this.state.value - 1
            }, function() {
                this.props.onChange(this.props.name, this.state.value)
            })
        }
    }

    onChangeHandler = (event) => {
        const target = event.target;
        const value = target.value;
        this.setState({
            value: parseInt(value)
        }, function() {
            this.props.onChange(this.props.name, this.state.value)
        })
    }

    render() {
        return(
            <div className="def-number-input number-input" style={this.props.style}>
                {
                    !this.props.noleft ?
                    <button className="minus" onClick={this.decrease}></button> :
                    <button className="minus disabled"></button>
                }
                <input className={'quantity ' + (this.props.readonly ? 'input-readonly' : '')} name={this.props.name}
                min={0}
                value={this.props.value}
                onChange={this.onChangeHandler.bind(this)}
                type="number"
                readOnly={this.props.readonly ? true : false} />
                {
                    !this.props.noright ?
                    <button className="plus" onClick={this.increase}></button> :
                    <button className="plus disabled"></button>
                }
            </div>
        )
    }
}

export default NumberInput;