import { 
    UPDATE_COOKIE_QTY,
    OPEN_COOKIE_BOX,
    OPEN_EDIT_COOKIE_BOX,
    CLOSE_COOKIE_BOX,
    ADD_COOKIE_BOX,
    UPDATE_COOKIE_BOX,
    REMOVE_COOKIE_BOX
} from '../actions/actionTypes';

const initialState = {
    items: {
        choco: {name: "Chocolate Chip"}
        //sugar: {name: "Sugar Cookie"}
    },
    box: null,
    type: null,
    mode: null,
    id: null,
    variety: {
        name: "Cookie Box",
        //description: "Assorted full-size gourmet cookies, fresh-baked daily",
        description: "Full-size gourmet chocolate chip cookies, fresh-baked daily",
        half: {
            price: 10.49,
            qty: 0,
            total: 0,
            list: []
        },
        dozen: {
            price: 18.99,
            qty: 0,
            total: 0,
            list: []
        },
        total: 0
    },
    sweet: {
        name: "Sweets & Coffee Break",
        description: "",
        small: {
            price: 36.99,
            qty: 0,
            total: 0,
            list: []
        },
        large: {
            price: 61.99,
            qty: 0,
            total: 0,
            list: []
        },
        total: 0
    },
    kettle: {
        name: "Kettle Potato Chips",
        description: "",
        single: {
            name: '1 bag',
            price: 1.00,
            qty: 0,
            total: 0,
            list: []
        },
        small: {
            name: '5 bags',
            price: 4.99,
            qty: 0,
            total: 0,
            list: []
        },
        large: {
            name: '10 bags',
            price: 9.99,
            qty: 0,
            total: 0,
            list: []
        },
        items: {
            sea: {name: 'Sea Salt'},
            //bbq: {name: 'BBQ'},
            layBake: {name: 'Lays Baked'}
        },
        total: 0
    },
    // kettle_small: {name: "5 bags", price: 4.99, qty: 0, total: 0},
    // kettle_large: {name: "10 bags", price: 9.99, qty: 0, total: 0},
    variety_half: {name: "Half Dozen", price: 10.49, qty:0, total:0, list: []},
    variety_dozen: {name: "Dozen", price: 18.99, qty:0, total:0, list: []},
    sweet_small: {name: "Sweets & Coffee Break Small", price: 36.99, qty: 0, total: 0, list: []},
    sweet_large: {name: "Sweets & Coffee Break Large", price: 61.99, qty: 0, total: 0, list: []},
    total: 0
}

const roundTo = require('roundto');

export default function(state = initialState, action) {
    switch (action.type) {
        case UPDATE_COOKIE_QTY : {
            const {name, qty} = action.payload;
            const ex_total = state[name].total;
            state[name].total = roundTo(state[name].price * qty, 2);
            state[name].qty = qty;
            
            const diff = roundTo(state[name].total - ex_total, 2);
            state.total = roundTo(state.total + diff, 2);

            return {...state};
        }
        case OPEN_COOKIE_BOX : {
            const {name, mode} = action.payload;
            const [box, type] = name.split('_');

            state.box = box;
            state.type = type;
            state.mode = mode;

            return {...state};
        }
        case OPEN_EDIT_COOKIE_BOX : {
            const {box, type, id} = action.payload;

            state.box = box;
            state.type = type;
            state.id = id;
            state.mode = 'edit';

            return {...state}
        }
        case ADD_COOKIE_BOX : {
            const {list} = action.payload;

            state[state.box][state.type].list.push(list);                                   
            state[state.box][state.type].qty = state[state.box][state.type].qty + 1;

            // Get Subtotal
            const box_total = state[state.box][state.type].total;
            state[state.box][state.type].total = roundTo(state[state.box][state.type].qty * state[state.box][state.type].price, 2);
            const diff = state[state.box][state.type].total - box_total;

            // Get Total
            state.total = roundTo(state.total + diff, 2);

            // Reset box, type, mode
            state.box = null;
            state.type = null;
            state.mode = null;

            return {...state}
        }
        case UPDATE_COOKIE_BOX : {
            const {list} = action.payload;

            state[state.box][state.type].list[state.id] = list;

            // Reset box, type, mode
            state.box = null;
            state.type = null;
            state.mode = null;

            return {...state}
        }
        case REMOVE_COOKIE_BOX: {
            const {box, type, id} = action.payload;

            state[box][type].list[id] = null;
            state[box][type].qty = state[box][type].qty - 1;
            
            let new_list = [];
            for (const id in state[box][type].list){
                if (state[box][type].list[id] !== null){
                    new_list.push(state[box][type].list[id])
                }
            }

            state[box][type].list = new_list;

            // Subtotal
            const box_total = state[box][type].total;
            state[box][type].total = roundTo(state[box][type].qty * state[box][type].price, 2);
            const diff = state[box][type].total - box_total;

            // Get Total
            state.total = state.total + diff;

            return {...state}
        }
        case CLOSE_COOKIE_BOX: {
            state.box = null;
            state.type = null;
            state.mode = null;

            return {...state};
        }
        default :
            return state;
    }
}