import React from 'react';
import { connect } from "react-redux";
import { Row, Col, ListGroup } from 'react-bootstrap';

// const roundTo = require('roundto');

class EggSandwich extends React.Component {    

    printListWithSub = (box, type, name) => {
        let box_count = 0;

        let print_subitem = this.props.egg_sandwich[box][type].list.map((list, index) => {
            const names = this.props.egg_sandwich[box].items;
            let print = [];
            let count = 0;
            let total_qty = 0;

            if (list === null){return null;}
            box_count++;
            
            for (let key in names) {
                if (list[key] !== 0){
                    if (typeof list[key] === 'undefined'){ return null; }
                    print.push(<li className="" key={'_child_' + count}> { names[key].name + ' : ' + list[key] } </li>);
                    total_qty = total_qty + list[key];
                    count++;
                }
            }
            
            return (
                <Col key={'ex_box' + index} md={6} style={{padding: '5px'}}>
                    <div style={{padding: '5px', borderBottom: '1px solid #e1e1e1'}}>
                        <h3 className="p-0" style={{fontSize: '1rem'}}>{box}-{type} #{box_count}</h3>
                        <ul key={index} className="list-inline" style={{paddingLeft: '10px', listStyle: 'none'}}> 
                            {print} 
                        </ul>
                    </div>
                </Col>
            )
        })

        return (
            <React.Fragment>

                <Row>
                    <Col className="item" md={5}>{name}</Col>
                    <Col className="qty">{this.props.egg_sandwich[box][type].qty}</Col>
                    <Col className="price">${this.props.egg_sandwich[box][type].price}</Col>
                    <Col className="total">${this.props.egg_sandwich[box][type].total}</Col>
                </Row>

                <Row className="item sub" style={{paddingTop: '10px'}}>
                    { print_subitem }
                </Row>

            </React.Fragment>
        )
    }

    render() {
        return (

            <ListGroup.Item style={{display: (this.props.egg_sandwich.total === 0 ? 'none' : null)}}>
                <h3>Egg Sandwiches</h3>
                <ul className="summary-list-group">
                    <li className="summary-list header">
                        <Row>
                            <Col className="item" md={5}>Item</Col>
                            <Col className="qty">Quantity</Col>
                            <Col className="price">Price</Col>
                            <Col className="total">Total</Col>
                        </Row>
                    </li>

                    <li className="summary-list" style={{display: (this.props.egg_sandwich.classic.half.qty === 0 ? 'none' : null)}}>
                        {this.printListWithSub('classic', 'half', 'Classic - Half Dozen')}
                    </li>

                    <li className="summary-list" style={{display: (this.props.egg_sandwich.classic.dozen.qty === 0 ? 'none' : null)}}>
                        {this.printListWithSub('classic', 'dozen', 'Classic - Dozen')}
                    </li>

                    <li className="summary-list" style={{display: (this.props.egg_sandwich.signature.half.qty === 0 ? 'none' : null)}}>
                        {this.printListWithSub('signature', 'half', 'Signature - Half Dozen')}
                    </li>

                    <li className="summary-list" style={{display: (this.props.egg_sandwich.signature.dozen.qty === 0 ? 'none' : null)}}>
                        {this.printListWithSub('signature', 'dozen', 'Signature - Dozen')}
                    </li>

                    <li className="summary-list" style={{display: (this.props.egg_sandwich.thintastic.half.qty === 0 ? 'none' : null)}}>
                        {this.printListWithSub('thintastic', 'half', 'Thintastic - Half Dozen')}
                    </li>

                    <li className="summary-list" style={{display: (this.props.egg_sandwich.thintastic.dozen.qty === 0 ? 'none' : null)}}>
                        {this.printListWithSub('thintastic', 'dozen', 'Thintastic - Dozen')}
                    </li>
                    

                    <li className="summary-list footer">
                        <Row>
                            <Col className="item" md={5}></Col>
                            <Col className="qty"></Col>
                            <Col className="price"></Col>
                            <Col className="total">${this.props.egg_sandwich.total}</Col>
                        </Row>
                    </li>
                </ul>
            </ListGroup.Item>
        )
    }
}

const mapStateToProps = (state) => ({
    egg_sandwich: state.egg_sandwich
});

export default connect(
    mapStateToProps
)(EggSandwich)