import { 
    UPDATE_BAGEL_SHEAMR_QTY,
    OPEN_BAGEL_SHEAMR_CHOICE,
    OPEN_EDIT_BAGEL_SHEAMR_CHOICE,
    CLOSE_BAGEL_SHEAMR_CHOICE,
    ADD_BAGEL_SHEAMR_CHOICE,
    UPDATE_BAGEL_SHEAMR_CHOICE,
    REMOVE_BAGEL_SHEAMR_CHOICE 
} from './actionTypes.js'

export const updateQty = (name, qty) => ({
    type: UPDATE_BAGEL_SHEAMR_QTY,
    payload: {
        name,
        qty
    }
})

export const openBox = (name, mode) => ({
    type: OPEN_BAGEL_SHEAMR_CHOICE,
    payload: {
        name,
        mode
    }
})

export const openEditBox = (box, id) => ({
    type: OPEN_EDIT_BAGEL_SHEAMR_CHOICE,
    payload: {
        box,
        id
    }
})

export const closeBox = () => ({
    type: CLOSE_BAGEL_SHEAMR_CHOICE
})

export const addBox = (item) => ({
    type: ADD_BAGEL_SHEAMR_CHOICE,
    payload: {
        item
    }
})

export const updateBox = (item) => ({
    type: UPDATE_BAGEL_SHEAMR_CHOICE,
    payload: {
        item
    }
})

export const removeBox = (box, id) => ({
    type: REMOVE_BAGEL_SHEAMR_CHOICE,
    payload: {
        box,
        id
    }
})