import {
    UPDATE_LUNCHSANDWICH_QTY,
    OPEN_LUNCHSANDWICH_BOX,
    OPEN_EDIT_LUNCHSANDWICH_BOX,
    CLOSE_LUNCHSANDWICH_BOX,
    ADD_LUNCHSANDWICH_BOX,
    UPDATE_LUNCHSANDWICH_BOX,
    REMOVE_LUNCHSANDWICH_BOX,
    REMOVE_LUNCHSANDWICH_CHOICE,
    ADD_LUNCHSANDWICH_LUNCHBOX,
    UPDATE_LUNCHSANDWICH_LUNCHBOX,
    REMOVE_LUNCHSANDWICH_LUNCHBOX
} from './actionTypes.js'

export const updateQty = (name, qty) => ({
    type: UPDATE_LUNCHSANDWICH_QTY,
    payload: {
        name,
        qty
    }
})

export const openBox = (name, mode) => ({
    type: OPEN_LUNCHSANDWICH_BOX,
    payload: {
        name,
        mode
    }
})

export const openEditBox = (box, type, id) => ({
    type: OPEN_EDIT_LUNCHSANDWICH_BOX,
    payload: {
        box,
        type,
        id
    }
})

export const closeBox = () => ({
    type: CLOSE_LUNCHSANDWICH_BOX
})

export const addBox = (item, box) => ({
    type: ADD_LUNCHSANDWICH_BOX,
    payload: {
        item,
        box
    }
})

export const updateBox = (item, box) => ({
    type: UPDATE_LUNCHSANDWICH_BOX,
    payload: {
        item,
        box
    }
})

export const removeBox = (box, type, id) => ({
    type: REMOVE_LUNCHSANDWICH_BOX,
    payload: {
        box,
        type,
        id
    }
})

export const removeCookie = (sandwich, cookie) => ({
    type: REMOVE_LUNCHSANDWICH_CHOICE,
    payload: {
        sandwich,
        cookie
    }
})

export const addLunchBox = (item) => ({
    type: ADD_LUNCHSANDWICH_LUNCHBOX,
    payload: {
        item
    }
})

export const updateLunchBox = (item) => ({
    type: UPDATE_LUNCHSANDWICH_LUNCHBOX,
    payload: {
        item
    }
})

export const removeLunchBox = (sandwich, id) => ({
    type: REMOVE_LUNCHSANDWICH_LUNCHBOX,
    payload: {
        sandwich,
        id
    }
})