import React from 'react';
import { connect } from "react-redux";
import { Row, Col, ListGroup } from 'react-bootstrap';

class FreshSalads extends React.Component {
    
    printList = (key, name) => {
        return (
            <li className="summary-list" style={{display: (this.props.fresh_salads[key].qty === 0 ? 'none' : null)}}>
                <Row>
                    <Col className="item" md={5}>{name}</Col>
                    <Col className="qty">{this.props.fresh_salads[key].qty}</Col>
                    <Col className="price">${this.props.fresh_salads[key].price}</Col>
                    <Col className="total">${this.props.fresh_salads[key].total}</Col>
                </Row>
            </li>
        )
    }

    printListWithSub = (box, name) => {
        let print_subitem = [];
        let subitem_index = 0;
        const names = this.props.fresh_salads.items;
        for (let key in this.props.fresh_salads[box].selected){
            let name = null;
            if (key in names.bagel){name = names.bagel[key].name;}
            else if (key in names.chips){name = names.chips[key].name;}
            print_subitem.push(<li className="" key={'_child_' + subitem_index}> { name + ' : ' + this.props.fresh_salads[box].selected[key] } </li>)
            subitem_index++;
        }

        print_subitem = (
            <Col md={6} style={{padding: '5px'}}>
                <div style={{padding: '5px', borderBottom: '1px solid #e1e1e1'}}>
                    <ul className="list-inline" style={{paddingLeft: '10px', listStyle: 'none'}}> 
                        {print_subitem} 
                    </ul>
                </div>
            </Col>
        )

        return (
            <React.Fragment>

                <Row>
                    <Col className="item" md={5}>{name}</Col>
                    <Col className="qty">{this.props.fresh_salads[box].qty}</Col>
                    <Col className="price">${this.props.fresh_salads[box].price}</Col>
                    <Col className="total">${this.props.fresh_salads[box].total}</Col>
                </Row>

                <Row className="item sub" style={{paddingTop: '10px'}}>
                    { print_subitem }
                </Row>

            </React.Fragment>
        )
    }

    render() {
        return (
            <ListGroup.Item style={{display: (this.props.fresh_salads.total === 0 ? 'none' : null)}}>
                <h3>Fresh Salads</h3>
                <ul className="summary-list-group">
                    <li className="summary-list header">
                        <Row>
                            <Col className="item" md={5}>Item</Col>
                            <Col className="qty">Quantity</Col>
                            <Col className="price">Price</Col>
                            <Col className="total">Total</Col>
                        </Row>
                    </li>

                    <li className="summary-list" style={{display: (this.props.fresh_salads.chicken.qty === 0 ? 'none' : null)}}>
                        {this.printListWithSub('chicken', 'Strawberry Chicken Salad - Single')}
                    </li>

                    {this.printList('chicken_group', 'Strawberry Chicken Salad - Group')}

                    <li className="summary-list" style={{display: (this.props.fresh_salads.almond.qty === 0 ? 'none' : null)}}>
                        {this.printListWithSub('almond', 'Strawberry Almond Salad - Single')}
                    </li>

                    {this.printList('almond_group', 'Strawberry Almond Salad - Group')}
                    
                    <li className="summary-list footer">
                        <Row>
                            <Col className="item" md={5}></Col>
                            <Col className="qty"></Col>
                            <Col className="price"></Col>
                            <Col className="total">${this.props.fresh_salads.total}</Col>
                        </Row>
                    </li>
                </ul>
            </ListGroup.Item>
        )
    }
}

const mapStateToProps = (state) => ({
    fresh_salads: state.fresh_salads
});

export default connect(
    mapStateToProps
)(FreshSalads)