import { 
    UPDATE_BK_QTY,
    OPEN_BK_BOX,
    OPEN_EDIT_BK_BOX,
    CLOSE_BK_BOX,
    ADD_BK_BOX,
    UPDATE_BK_BOX,
    REMOVE_BK_BOX,
    ADD_BK_PASTRY,
    UPDATE_BK_PASTRY,
    CLOSE_BK_PASTRY,
    REMOVE_BK_PASTRY
} from '../actions/actionTypes';

const initialState = {
    box: null,
    mode: null,
    id: null,
    for_group: {
        name: 'Breakfast For The Group', 
        price: 129.99, 
        qty: 0, 
        bagel_max: 12,
        whip_max: 2,
        total: 0, 
        description: 'A Mixed Bagels & Sweets Nosh Box, Large Fresh Fruit Salad, Coffee & Orange Juice for the Group & 2 Tubs of Double-Whipped Shmear',
        list: []
    },
    salmon_platter: {
        name: 'Traditional Nova Lox Salmon Platter', 
        price: 94.99, 
        qty: 0, 
        bagel_max: 13,
        whip_max: 2,
        total: 0, 
        description: 'Complete with 13 Fresh-Baked Bagels, Capers, Tomatoes, Red Onions, Cucumbers, Lemons & 2 Tubs of Double-Whipped Shmear',
        list: []
    },
    nosh_box: {
        name: 'Mixed Bagels & Sweets Nosh Box', 
        price: 45.99, 
        qty: 0, 
        bagel_max: 12,
        whip_max: 2,
        total: 0, 
        description: 'An assortment of 9 Breakfast Muffins & Assorted Pastries plus 12 Bagels with 2 Tubs of Double-Whipped Shmear & Preserves',
        list: []
    },
    bagel_shmear_break_box: {
        name: 'Bagel & Shmear Breakfast Box', 
        price: 7.99, 
        qty: 0, 
        bagel_max: 1,
        whip_max: 1,
        total: 0, 
        description: 'Bagel with Shmear, Fruit Cup, Yogurt with Granola & a Banana',
        list: []
    },
    power_protein_breakfast_box: {name: 'Power Protein Breakfast Box', price: 7.99, qty: 0, total: 0, description: 'Wheat Bagel with Peanut Butter, Fruit Cup, Yogurt with Granola & a Banana'},
    Pastry_breakfast_box: {
        name: 'Pastry Breakfast Box', 
        price: 7.99, 
        qty: 0, 
        total: 0, 
        description: 'Choice of Muffin or Strudel, Fruit Cup, Yogurt with Granola & a Banana',
        list: {},
        items: {
            blueberry: {name: 'Blueberry Muffin'},
            cinnamon: {name: 'Cinnamon Chip Muffin'},
            cherry: {name: 'Greek Yogurt Cherry'}

        }
    },
    total: 0,
    bagel: {
        plain: {name: 'Plain'},
        honey: {name: 'Honey Whole Wheat'},
        everything: {name: 'Everything'},
        asiago: {name: 'Asiago'},
        blueberry: {name: 'Blueberry'},
        choco: {name: 'Chocolate Chip'},
        cinnamonRaisin: {name: 'Cinnamon Raisin'},
        cinnamonSugar: {name: 'Cinnamon Sugar'},
        sesame: {name: 'Sesame Seed'}
    },
    whip: {
        plain: {name: 'Plain'},
        onion: {name: 'Onion & Chive'},
        salmon: {name: 'Smoked Salmon'},
        plainRF: {name: 'Plain'},
        garden: {name: 'Garden Veggie'},
        honey: {name: 'Honey Almond'},
        jalapeno: {name: 'Jalapeno Salsa'},
        strawberry: {name: 'Strawberry'}
    },
}

const roundTo = require('roundto');

export default function(state = initialState, action) {
    switch (action.type) {
        case UPDATE_BK_QTY : {
            const {name, qty} = action.payload;
            state[name].total = roundTo(state[name].price * qty, 2);
            state[name].qty = qty;
            state.total = roundTo(
                                state.for_group.total + 
                                state.salmon_platter.total +
                                state.nosh_box.total +
                                state.bagel_shmear_break_box.total +
                                state.power_protein_breakfast_box.total +
                                state.Pastry_breakfast_box.total
                                ,2);

            return {...state};
        }
        case OPEN_BK_BOX : {
            const {name, mode} = action.payload;

            state.box = name;
            state.mode = mode;

            return {...state};
        }
        case OPEN_EDIT_BK_BOX : {
            const {box, id} = action.payload;

            state.box = box;
            state.id = id;
            state.mode = 'edit';

            return {...state}
        }
        case ADD_BK_BOX : {
            const {list} = action.payload;

            state[state.box].list.push(list);                                   
            state[state.box].qty = state[state.box].qty + 1;

            // Get Subtotal
            const box_total = state[state.box].total;
            state[state.box].total = roundTo(state[state.box].qty * state[state.box].price, 2);
            const diff = state[state.box].total - box_total;

            // Get Total
            state.total = roundTo(state.total + diff, 2);

            // Reset box, type, mode
            state.box = null;
            state.mode = null;

            return {...state}
        }
        case UPDATE_BK_BOX : {
            const {list} = action.payload;

            state[state.box].list[state.id] = list;

            // Reset box, type, mode
            state.box = null;
            state.mode = null;

            return {...state}
        }
        case REMOVE_BK_BOX: {
            const {box, id} = action.payload;

            state[box].list[id] = null;
            state[box].qty = state[box].qty - 1;

            let new_list = [];
            for (const id in state[box].list){
                if (state[box].list[id] !== null){
                    new_list.push(state[box].list[id])
                }
            }
            state[box].list = new_list;

            // Subtotal
            const box_total = state[box].total;
            state[box].total = roundTo(state[box].qty * state[box].price, 2);
            const diff = state[box].total - box_total;

            // Get Total
            state.total = state.total + diff;

            return {...state}
        }
        case CLOSE_BK_BOX: {
            state.box = null;
            state.mode = null;

            return {...state};
        }
        case ADD_BK_PASTRY: {
            const {item} = action.payload;
            // let box_total = 0;
            let diff = 0;

            if (item in state.Pastry_breakfast_box.list) {
                state.Pastry_breakfast_box.list[item] = state.Pastry_breakfast_box.list[item] + 1;
            }
            else {
                state.Pastry_breakfast_box.list[item] = 1;
            }

            state.Pastry_breakfast_box.qty = state.Pastry_breakfast_box.qty + 1;
            state.Pastry_breakfast_box.total = roundTo(state.Pastry_breakfast_box.price*state.Pastry_breakfast_box.qty, 2);
            
            diff = state.Pastry_breakfast_box.price;

            // Get Total
            state.total = roundTo(state.total + diff, 2);

            // Reset box, type, mode
            state.box = null;
            state.type = null;
            state.mode = null;

            return {...state}
        }
        case UPDATE_BK_PASTRY: {
            const {item} = action.payload;

            // Decrement previous item


            state.Pastry_breakfast_box.list[state.id] = item;

            // Reset box, type, mode
            state.box = null;
            state.type = null;
            state.mode = null;

            return {...state}
        }
        case REMOVE_BK_PASTRY: {
            const {item} = action.payload;

            state.Pastry_breakfast_box.list[item] = state.Pastry_breakfast_box.list[item] - 1;
            if (state.Pastry_breakfast_box.list[item] === 0) {
                // delete state.lunchBox[sandwich].list[cookie]
                let new_list = {}
                for (let key in state.Pastry_breakfast_box.list){
                    if (state.Pastry_breakfast_box.list[key] !== 0){
                        new_list[key] = state.Pastry_breakfast_box.list[key]
                    }
                }
                state.Pastry_breakfast_box.list = new_list;
            }
            // console.log(state.Pastry_breakfast_box.list)

            // Subtotal
            state.Pastry_breakfast_box.qty = state.Pastry_breakfast_box.qty - 1;
            state.Pastry_breakfast_box.total = roundTo(state.Pastry_breakfast_box.total - state.Pastry_breakfast_box.price, 2)
            state.total = roundTo(state.total - state.Pastry_breakfast_box.price, 2)

            return {...state}
        }
        case CLOSE_BK_PASTRY: {
            state.box = null;
            state.type = null;
            state.mode = null;

            return {...state}
        }
        default :
            return state;
    }
}