import { 
    UPDATE_BAGEL_SHEAMR_QTY,
    OPEN_BAGEL_SHEAMR_CHOICE,
    OPEN_EDIT_BAGEL_SHEAMR_CHOICE,
    CLOSE_BAGEL_SHEAMR_CHOICE,
    ADD_BAGEL_SHEAMR_CHOICE,
    UPDATE_BAGEL_SHEAMR_CHOICE,
    REMOVE_BAGEL_SHEAMR_CHOICE 
} from '../actions/actionTypes';

const initialState = {
    box: null,
    mode: null,
    id: null,
    baker_dozen: {
        name: "Bagels & Shmear Baker's Dozen",
        description: "13 Fresh-Baked Bagels & 2 Tubs of Double-Whipped Shmear",
        price: 17.99,
        qty: 0,
        total: 0,
        list: []
    },
    half_dozen: {
        name: "Bagels & Shmear Half Dozen",
        description: "6 Fresh-Baked Bagels & 1 Tub of Double-Whipped Shmear",
        price: 10.45,
        qty: 0,
        total: 0,
        list: []
    },
    nosh_box: {
        name: "Bagels & Shmear Nosh Box",
        description: "24 Fresh-Baked Bagels & 4 Tubs of our Double-Whipped Shmear",
        price: 32.49,
        qty: 0,
        total: 0,
        list: []
    },
    bagel: {
        plain: {name: 'Plain'},
        honey: {name: 'Honey Whole Wheat'},
        everything: {name: 'Everything'},
        asiago: {name: 'Asiago'},
        blueberry: {name: 'Blueberry'},
        choco: {name: 'Chocolate Chip'},
        cinnamonRaisin: {name: 'Cinnamon Raisin'},
        cinnamonSugar: {name: 'Cinnamon Sugar'},
        sesame: {name: 'Sesame Seed'}
    },
    whip: {
        name: "Shmear By The Tub",
        description: "Single 6oz Tub of Our Double-Whipped Shmear",
        qty: 0,
        total: 0,
        list: [],
        price : 3.69,
        plain: {name: 'Plain'},
        onion: {name: 'Onion & Chive'},
        salmon: {name: 'Smoked Salmon'},
        plainRF: {name: 'Plain'},
        garden: {name: 'Garden Veggie'},
        honey: {name: 'Honey Almond'},
        jalapeno: {name: 'Jalapeno Salsa'},
        strawberry: {name: 'Strawberry'}
    },
    total: 0
}

const roundTo = require('roundto');

export default function(state = initialState, action) {
    switch (action.type) {
        case UPDATE_BAGEL_SHEAMR_QTY : {
            const {name, qty} = action.payload;
            state[name].total = roundTo(state[name].price * qty, 2);
            state[name].qty = qty;
            state.total = roundTo(state.baker_dozen.total + state.nosh_box.total, 2);

            return {...state};
        }
        case OPEN_BAGEL_SHEAMR_CHOICE : {
            const {name, mode} = action.payload;


            state.box = name;
            state.mode = mode;

            return {...state};
        }
        case OPEN_EDIT_BAGEL_SHEAMR_CHOICE : {
            const {box, id} = action.payload;

            state.box = box;
            state.id = id;
            state.mode = 'edit';

            return {...state}
        }
        case ADD_BAGEL_SHEAMR_CHOICE : {
            const {item} = action.payload;
            let box_total = 0;
            let diff = 0;

            state[state.box].list.push(item);
            state[state.box].qty = state[state.box].qty + 1;

            box_total = state[state.box].total;
            state[state.box].total = roundTo(state[state.box].qty * state[state.box].price, 2);
            diff = state[state.box].total - box_total;

            // Get Total
            state.total = roundTo(state.total + diff, 2);

            // Reset box, mode
            state.box = null;
            state.mode = null;

            return {...state}
        }
        case UPDATE_BAGEL_SHEAMR_CHOICE : {
            const {item} = action.payload;

            // Decrement previous item


            state[state.box].list[state.id] = item;

            // Reset box, type, mode
            state.box = null;
            state.mode = null;

            return {...state}
        }
        case REMOVE_BAGEL_SHEAMR_CHOICE: {
            const {box, id} = action.payload;

            let box_total = 0;
            let diff = 0;

            state[box].list[id] = null;
            state[box].list = state[box].list.filter(function (box) {
                return box != null
            })
            state[box].qty = state[box].qty - 1;

            // Subtotal
            box_total = state[box].total;
            state[box].total = roundTo(state[box].qty * state[box].price, 2);
            diff = state[box].total - box_total;
            

            // Get Total
            state.total = roundTo(state.total + diff, 2);

            return {...state}
        }
        case CLOSE_BAGEL_SHEAMR_CHOICE: {
            state.box = null;
            state.mode = null;

            return {...state};
        }
        default :
            return state;
    }
}