import {
    OPEN_EGG_SANDWICH_BOX,
    OPEN_EDIT_EGG_SANDWICH_BOX,
    CLOSE_EGG_SANDWICH_BOX,
    ADD_EGG_SANDWICH_BOX,
    UPDATE_EGG_SANDWICH_BOX,
    REMOVE_EGG_SANDWICH_BOX
} from './actionTypes.js'

export const openBox = (name, mode) => ({
    type: OPEN_EGG_SANDWICH_BOX,
    payload: {
        name,
        mode
    }
})

export const openEditBox = (box, type, id) => ({
    type: OPEN_EDIT_EGG_SANDWICH_BOX,
    payload: {
        box,
        type,
        id
    }
})

export const closeBox = () => ({
    type: CLOSE_EGG_SANDWICH_BOX
})

export const addBox = (list) => ({
    type: ADD_EGG_SANDWICH_BOX,
    payload: {
        list
    }
})

export const updateBox = (list) => ({
    type: UPDATE_EGG_SANDWICH_BOX,
    payload: {
        list
    }
})

export const removeBox = (box, type, id) => ({
    type: REMOVE_EGG_SANDWICH_BOX,
    payload: {
        box,
        type,
        id
    }
})