import {
    UPDATE_BK_QTY,
    OPEN_BK_BOX,
    OPEN_EDIT_BK_BOX,
    CLOSE_BK_BOX,
    ADD_BK_BOX,
    UPDATE_BK_BOX,
    REMOVE_BK_BOX,
    ADD_BK_PASTRY,
    UPDATE_BK_PASTRY,
    CLOSE_BK_PASTRY,
    REMOVE_BK_PASTRY
} from './actionTypes.js'

export const updateQty = (name, qty) => ({
    type: UPDATE_BK_QTY,
    payload: {
        name,
        qty
    }
})

export const openBox = (name, mode) => ({
    type: OPEN_BK_BOX,
    payload: {
        name,
        mode
    }
})

export const openEditBox = (box, id) => ({
    type: OPEN_EDIT_BK_BOX,
    payload: {
        box,
        id
    }
})

export const closeBox = () => ({
    type: CLOSE_BK_BOX
})

export const addBox = (list) => ({
    type: ADD_BK_BOX,
    payload: {
        list
    }
})

export const updateBox = (list) => ({
    type: UPDATE_BK_BOX,
    payload: {
        list
    }
})

export const removeBox = (box, id) => ({
    type: REMOVE_BK_BOX,
    payload: {
        box,
        id
    }
})

export const closePastry = () => ({
    type: CLOSE_BK_PASTRY
})

export const addPastry = (item) => ({
    type: ADD_BK_PASTRY,
    payload: {
        item
    }
})

export const updatePastry = (item) => ({
    type: UPDATE_BK_PASTRY,
    payload: {
        item
    }
})

export const removePastry = (item) => ({
    type: REMOVE_BK_PASTRY,
    payload: {
        item
    }
})