const roundTo = require('roundto');

export default function(state = initialState, action) {
    switch (action.type) {
        case UPDATE_LUNCHSANDWICH_QTY : {
            const {name, qty} = action.payload;

            // Get total
            const subtotal = state[name].total;
            
            state[name].total = roundTo(state[name].price * qty, 2);
            state[name].qty = qty;

            const diff = state[name].total - subtotal;

            state.total = roundTo(state.total + diff, 2);            

            return {...state};
        }
        case OPEN_LUNCHSANDWICH_BOX : {
            const {name, mode} = action.payload;
            const [box, type] = name.split('_');

            state.box = box;
            state.type = (type === 'null' ? null : type);
            state.mode = mode;

            return {...state};
        }
        case OPEN_EDIT_LUNCHSANDWICH_BOX : {
            const {box, type, id} = action.payload;

            state.box = box;
            state.type = type;
            state.id = id;
            state.mode = 'edit';

            return {...state}
        }
        case ADD_LUNCHSANDWICH_BOX : {
            const {item, box} = action.payload;
            let box_total = 0;
            let diff = 0;

            console.log(box);
            if (state.box === 'forGroup'){
                state.forGroup.list.push(item);
                state.forGroup.qty = state.forGroup.qty + 1;

                // Get Subtotal
                box_total = state.forGroup.total;
                state.forGroup.total = roundTo(state.forGroup.qty * state.forGroup.price, 2);
                diff = state.forGroup.total - box_total;
            }
            else if (state.box === 'lunchBox'){
                if (item in state.lunchBox[state.type].list) {
                    state.lunchBox[state.type].list[item] = state.lunchBox[state.type].list[item] + 1;
                }
                else {
                    state.lunchBox[state.type].list[item] = 1;
                }
                state.lunchBox[state.type].qty = state.lunchBox[state.type].qty + 1;
                state.lunchBox[state.type].total = roundTo(state.lunchBox[state.type].price*state.lunchBox[state.type].qty, 2);
                state.lunchBox.qty = state.lunchBox.qty + 1;
                
                state.lunchBox.total = roundTo(state.lunchBox.total + state.lunchBox[state.type].price, 2);
                diff = state.lunchBox[state.type].price;
            }
             else {
                state[state.box][state.type].list.push(item);                                   
                state[state.box][state.type].qty = state[state.box][state.type].qty + 1;

                // Get Subtotal
                box_total = state[state.box][state.type].total;
                state[state.box][state.type].total = roundTo(state[state.box][state.type].qty * state[state.box][state.type].price, 2);
                diff = state[state.box][state.type].total - box_total;
             }

            // Get Total
            state.total = roundTo(state.total + diff, 2);

            // Reset box, type, mode
            state.box = null;
            state.type = null;
            state.mode = null;

            return {...state}
        }
        case UPDATE_LUNCHSANDWICH_BOX : {
            const {item, box} = action.payload;

            // Decrement previous item

        
            if (state.box === 'forGroup') {
                state.forGroup.list[state.id] = item;
            } else {
            state[box].list[state.id] = item;
            }

            // Reset box, type, mode
            state.box = null;
            state.type = null;
            state.mode = null;

            return {...state}
        }
        case REMOVE_LUNCHSANDWICH_BOX: {
            const {box, type, id} = action.payload;

            let box_total = 0;
            let diff = 0;

            if (box === 'forGroup'){

                state.forGroup.list[id] = null;
                state.forGroup.list = state.forGroup.list.filter(function (box) {
                    return box != null
                })
                state.forGroup.qty = state.forGroup.qty - 1;

                // Subtotal
                box_total = state.forGroup.total;
                state.forGroup.total = roundTo(state.forGroup.qty * state.forGroup.price, 2);
                diff = state.forGroup.total - box_total;

            }
            else {

                state[box][type].list[id] = null;
                state[box][type].list = state[box][type].list.filter(function (box) {
                    return box != null
                })
                state[box][type].qty = state[box][type].qty - 1;

                // Subtotal
                box_total = state[box][type].total;
                state[box][type].total = roundTo(state[box][type].qty * state[box][type].price, 2);
                diff = state[box][type].total - box_total;

            }

            // Get Total
            state.total = roundTo(state.total + diff, 2);

            return {...state}
        }
        case REMOVE_LUNCHSANDWICH_CHOICE: {
            const {sandwich, cookie} = action.payload;

            state.lunchBox[sandwich].list[cookie] = state.lunchBox[sandwich].list[cookie] - 1;
            if (state.lunchBox[sandwich].list[cookie] === 0) {
                // delete state.lunchBox[sandwich].list[cookie]
                let new_list = {}
                for (let key in state.lunchBox[sandwich].list){
                    if (state.lunchBox[sandwich].list[key] !== 0){
                        new_list[key] = state.lunchBox[sandwich].list[key]
                    }
                }
                state.lunchBox[sandwich].list = new_list;
            }

            // Subtotal
            state.lunchBox[sandwich].qty = state.lunchBox[sandwich].qty - 1;
            state.lunchBox.qty = state.lunchBox.qty - 1;
            state.lunchBox.total = roundTo(state.lunchBox.total - state.lunchBox[sandwich].price, 2)
            state.total = roundTo(state.total - state.lunchBox[sandwich].price, 2)

            return {...state}
        }
        case CLOSE_LUNCHSANDWICH_BOX: {
            state.box = null;
            state.type = null;
            state.mode = null;

            return {...state};
        }
        case ADD_LUNCHSANDWICH_LUNCHBOX: {
            const {item} = action.payload;
            let box_total = 0;
            let diff = 0;

            state.lunchBox[state.type].list.push(item);
            state.lunchBox[state.type].qty = state.lunchBox[state.type].qty + 1;
            state.lunchBox[state.type].total = state.lunchBox[state.type].qty * state.lunchBox[state.type].price; //had to add [state.type] here to get right price because of price differences between items. Price is now a prop of lunchbox.type (or lunchbox[state.type]) not lunchbox

            // Get Subtotal
            box_total = state.lunchBox.total;
            state.lunchBox.qty = state.lunchBox.qty + 1;
            state.lunchBox.total = roundTo(state.lunchBox.total + state.lunchBox[state.type].price, 2);
            // state.lunchBox.total = roundTo(state.lunchBox.qty * state.lunchBox[state.type].price, 2); //had to add [state.type] here to get right price because of price differences between items. Price is now a prop of lunchbox.type (or lunchbox[state.type]) not lunchbox
            diff = state.lunchBox.total - box_total;
            
            // Get Total
            state.total = roundTo(state.total + diff, 2);

            // Reset box, type, mode
            state.box = null;
            state.type = null;
            state.mode = null;

            return {...state}
        }
        case UPDATE_LUNCHSANDWICH_LUNCHBOX: {
            const {item} = action.payload;

            state.lunchBox[state.type].list[state.id] = item;

            // Reset box, type, mode
            state.box = null;
            state.type = null;
            state.mode = null;

            return {...state}
        }
        case REMOVE_LUNCHSANDWICH_LUNCHBOX: {
            const {sandwich, id} = action.payload;

            let new_list = [];
            state.lunchBox[sandwich].list[id] = null;
            for (const list_id in state.lunchBox[sandwich].list){
                if (state.lunchBox[sandwich].list[list_id] !== null){
                    new_list.push(state.lunchBox[sandwich].list[list_id])
                }
            }
            state.lunchBox[sandwich].list = new_list;

            state.lunchBox[sandwich].qty = state.lunchBox[sandwich].qty - 1;
            state.lunchBox[sandwich].total = state.lunchBox[sandwich].qty * state.lunchBox[sandwich].price;

            let box_total = 0;
            let diff = 0;
            // Get Subtotal
            box_total = state.lunchBox.total;
            state.lunchBox.qty = state.lunchBox.qty - 1;
            state.lunchBox.total = roundTo(state.lunchBox.total - state.lunchBox[sandwich].price, 2);
            // state.lunchBox.total = roundTo(state.lunchBox.qty * state.lunchBox[sandwich].price, 2);
            diff = state.lunchBox.total - box_total;

            // Get Total
            state.total = roundTo(state.total + diff, 2);

            return {...state}
        }
        default :
            return state;
    }
}