import React from "react";
import { connect } from "react-redux";

import { Row, Col, Card, ListGroup, ButtonToolbar, Button } from 'react-bootstrap';
import NumberInput from '../common/numberInput.js';
import TopNav from '../common/TopNav.js';

import { updateCurrentNav } from '../../redux/actions/main';
import { openBox, openEditBox, removeBox } from '../../redux/actions/egg_sandwiches_action';

import BoxController from './BoxController';
import Bottom from '../bottom';

// import styles from '../common/style.js';

// const subHeader = {
//     marginTop: '1rem',
//     marginBottom: '10px',
//     paddingLeft: '20px',
//     fontWeight: '600',
//     fontSize: '17px'
// }
// const style_listGroup = {
//     padding: '0px',
//     paddingLeft: '20px',
//     borderBottom: '1px solid #dadada'
// } 

class EggSandwich extends React.Component {
    constructor(props) {
        super(props);
        
        this.state = {
            total: this.props.egg_sandwich.total
        }

        this.props.updateCurrentNav(1);
    }

    // onChangeHandler = (name, value) => {
    //     this.props.updateQty(name, value);
    // }

    openBox = (name) => {
        this.props.openBox(name, 'add');
    }

    openEditBox = (box, type, id) => {
        this.props.openEditBox(box, type, id);
    }

    removeBox = (box, type, id) => {
        this.props.removeBox(box, type, id);
    }

    printSelectedBox = (box, type, list, index, num) => {
        const names = this.props.egg_sandwich[box].items;
        let print = [];
        let count = 0;
        let total_qty = 0;

        for (let key in names) {
            if (list[key] !== 0){
                if (typeof list[key] === 'undefined'){ return null; }
                print.push(<li className="" key={'_child_' + count}> { names[key].name + ' : ' + list[key] } </li>);
                total_qty = total_qty + list[key];
                count++;
            }
        }

        if (total_qty === 0){return null;}
        
        return (
            <Col md={4} style={{padding: '5px'}} key={'ex_box' + num}>
                <div key={index} className="selected_box_wrap" style={{padding: '5px', border: '1px solid #e1e1e1'}}>
                    <h3 className="p-0" style={{fontSize: '1rem'}}>#{num} {box} {type}
                        <ButtonToolbar style={{float: 'right'}}>
                            <Button onClick={() => {this.openEditBox(box, type, index)}} className="edit-btn">Edit</Button>
                            <Button onClick={() => {this.removeBox(box, type, index)}} className="remove-btn">Remove</Button>
                        </ButtonToolbar>
                    </h3>
                    <ul key={index} className="" style={{height: '', paddingLeft: '10px', listStyle: 'none'}}> {print} </ul>
                </div>
            </Col>
        )
    }

    printRow = (name, box, type, qty, list) => {
        let count = 0;
        return (
            <React.Fragment>

                <Row>
                    <Col className="wrap_qty">
                        <NumberInput 
                            noleft={true}
                            readonly={true}
                            onChange={this.openBox.bind(this)}
                            name={box + '_' + type}
                            value={qty}
                        />
                        <p style={{float: 'right', fontWeight: '600'}}>{name} - ${this.props.egg_sandwich[box][type].price}</p> 
                    </Col>
                </Row>

                {
                    qty === 0 ?
                    null
                    :
                    <Row style={{marginBottom: '1rem'}}>
                        {
                            list.map((row, index) => {
                                if (row === null){return null;}
                                else {
                                    count++;
                                    return this.printSelectedBox(box, type, row, index, count)
                                }
                            })
                        }
                    </Row>
                }
            </React.Fragment>
        )
    }

    render() {
        switch (this.props.egg_sandwich.box) {
            case 'classic': {
                return <BoxController 
                            box={this.props.egg_sandwich.box}
                            type={this.props.egg_sandwich.type}
                            mode={this.props.egg_sandwich.mode}
                            id={this.props.egg_sandwich.id}
                       />
            }
            case 'signature': {
                return <BoxController 
                            box={this.props.egg_sandwich.box}
                            type={this.props.egg_sandwich.type}
                            mode={this.props.egg_sandwich.mode}
                            id={this.props.egg_sandwich.id}
                       />
            }
            case 'thintastic': {
                return <BoxController 
                            box={this.props.egg_sandwich.box}
                            type={this.props.egg_sandwich.type}
                            mode={this.props.egg_sandwich.mode}
                            id={this.props.egg_sandwich.id}
                       />
            }
            default : {
                return(
                    <Card className="col-md-12 p-0 menu-card">
        
                        <Card.Header>
                            <TopNav />
                        </Card.Header>
        
                        <Card.Body>
                            <Card.Title>Egg Sandwiches</Card.Title>
                        </Card.Body>
        
                        <ListGroup style={{marginTop: '15px'}}>

                            <ListGroup.Item>

                                <Row>
                                    <Col className="wrap_name">
                                        <p className="item-header" style={{marginBottom: '0px', paddingLeft: '0px', color: '#b36a3b'}}>{this.props.egg_sandwich.classic.name}</p>
                                        <p className="item-description">{this.props.egg_sandwich.classic.description}</p>
                                    </Col>
                                </Row>

                                { 
                                    this.printRow(
                                        'Half Dozen', 
                                        "classic",
                                        "half",
                                        this.props.egg_sandwich.classic.half.qty, 
                                        this.props.egg_sandwich.classic.half.list
                                    ) 
                                }

                                { 
                                    this.printRow(
                                        'Dozen', 
                                        "classic",
                                        "dozen",
                                        this.props.egg_sandwich.classic.dozen.qty, 
                                        this.props.egg_sandwich.classic.dozen.list
                                    ) 
                                }

                            </ListGroup.Item>
        
                        </ListGroup>

                        <ListGroup style={{marginTop: '15px'}}>

                            <ListGroup.Item>

                                <Row>
                                    <Col className="wrap_name">
                                        <p className="item-header" style={{marginBottom: '0px', paddingLeft: '0px', color: '#b36a3b'}}>{this.props.egg_sandwich.signature.name}</p>
                                        <p className="item-description">{this.props.egg_sandwich.signature.description}</p>
                                    </Col>
                                </Row>

                                { 
                                    this.printRow(
                                        'Half Dozen', 
                                        "signature",
                                        "half",
                                        this.props.egg_sandwich.signature.half.qty, 
                                        this.props.egg_sandwich.signature.half.list
                                    ) 
                                }

                                { 
                                    this.printRow(
                                        'Dozen', 
                                        "signature",
                                        "dozen",
                                        this.props.egg_sandwich.signature.dozen.qty, 
                                        this.props.egg_sandwich.signature.dozen.list
                                    ) 
                                }

                            </ListGroup.Item>
        
                        </ListGroup>

                        <ListGroup style={{marginTop: '15px'}}>

                            <ListGroup.Item>

                                <Row>
                                    <Col className="wrap_name">
                                        <p className="item-header" style={{marginBottom: '0px', paddingLeft: '0px', color: '#b36a3b'}}>{this.props.egg_sandwich.thintastic.name}</p>
                                        <p className="item-description">{this.props.egg_sandwich.thintastic.description}</p>
                                    </Col>
                                </Row>

                                { 
                                    this.printRow(
                                        'Half Dozen', 
                                        "thintastic",
                                        "half",
                                        this.props.egg_sandwich.thintastic.half.qty, 
                                        this.props.egg_sandwich.thintastic.half.list
                                    ) 
                                }

                                { 
                                    this.printRow(
                                        'Dozen', 
                                        "thintastic",
                                        "dozen",
                                        this.props.egg_sandwich.thintastic.dozen.qty, 
                                        this.props.egg_sandwich.thintastic.dozen.list
                                    ) 
                                }

                            </ListGroup.Item>

                            <ListGroup.Item className="menu-list es-list-group-item total">
                                <Row>
                                    <Col>
                                        ${this.props.egg_sandwich.total}
                                    </Col>
                                </Row>
                            </ListGroup.Item>
        
                        </ListGroup>
        
                        <Row>
                            <Col md={12}>
                                <Bottom />
                            </Col>
                        </Row>
        
                    </Card>
                )
            }
        }

    }
}

const mapStateToProps = (state) => ({
    egg_sandwich: state.egg_sandwich
});

const mapDispatchToProps = dispatch => (
    {
        openBox: (name, mode) => dispatch(openBox(name, mode)),
        openEditBox: (box, type, id) => dispatch(openEditBox(box, type, id)),
        removeBox: (box, type, id) => dispatch(removeBox(box, type, id)),
        updateCurrentNav: (index) => dispatch(updateCurrentNav(index))
    }
)

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(EggSandwich)