import { 
    UPDATE_FRESHSALADS_QTY,
    OPEN_FRESHSALADS_BOX,
    OPEN_EDIT_FRESHSALADS_BOX,
    CLOSE_FRESHSALADS_BOX,
    ADD_FRESHSALADS_BOX,
    UPDATE_FRESHSALADS_BOX,
    REMOVE_FRESHSALADS_BOX,
    REMOVE_FRESHSALADS_CHOICE
} from '../actions/actionTypes';

const initialState = {
    box: null,
    mode: null,
    id: null,
    items: {
        bagel: {
            plain: {name: 'Plain'},
            sesame: {name: 'Sesame'},
            asiago: {name: 'Asiago'}
        },
        chips: {
            bakedlays: {name: 'Baked Lays'},
            kettleseasalt: {name: 'Kettle Plain Sea Salt'},
            //kettlebbq: {name: 'Kettle BBQ'}
        }
    },
    chicken: {
        price: 8.99,
        qty: 0,
        selected: {},
        list: [],
        total: 0
    },
    chicken_group: {name: '', price: 42.99, qty: 0, total: 0}, 
    almond: {
        price: 8.99,
        qty: 0,
        selected: {},
        list: [],
        total: 0
    },
    almond_group: {name: '', price: 36.99, qty: 0, total: 0}, 
    total: 0
}

const roundTo = require('roundto');

export default function(state = initialState, action) {
    switch (action.type) {
        case UPDATE_FRESHSALADS_QTY : {
            const {name, qty} = action.payload;

            // Get total
            const subtotal = state[name].total;
            
            state[name].total = roundTo(state[name].price * qty, 2);
            state[name].qty = qty;

            const diff = state[name].total - subtotal;

            state.total = roundTo(state.total + diff, 2);            

            return {...state};
        }
        case OPEN_FRESHSALADS_BOX : {
            const {box, mode} = action.payload;

            state.box = box;
            state.mode = mode;

            return {...state};
        }
        case OPEN_EDIT_FRESHSALADS_BOX : {
            const {box, id} = action.payload;

            state.box = box;
            state.id = id;
            state.mode = 'edit';

            return {...state}
        }
        case ADD_FRESHSALADS_BOX : {
            const {item} = action.payload;

            state[state.box].list.push(item);                                   
            state[state.box].qty = state[state.box].qty + 1;
            
            // Add Choice
            if (item in state[state.box].selected) {
                state[state.box].selected[item] = state[state.box].selected[item] + 1;
            }
            else {
                state[state.box].selected[item] = 1;
            }

            // Get Subtotal
            const box_total = state[state.box].total;
            state[state.box].total = roundTo(state[state.box].qty * state[state.box].price, 2);
            const diff = state[state.box].total - box_total;

            // Get Total
            state.total = roundTo(state.total + diff, 2);

            // Reset box, type, mode
            state.box = null;
            state.mode = null;

            return {...state}
        }
        case UPDATE_FRESHSALADS_BOX : {
            const {item} = action.payload;

            // Decrement previous item


            state[state.box].list[state.id] = item;

            // Reset box, type, mode
            state.box = null;
            state.mode = null;

            return {...state}
        }
        case REMOVE_FRESHSALADS_BOX: {
            const {box, id} = action.payload;

            state[box].list[id] = null;
            state[box].qty = state[box].qty - 1;

            // Subtotal
            const box_total = state[box].total;
            state[box].total = roundTo(state[box].qty * state[box].price, 2);
            const diff = state[box].total - box_total;

            // Get Total
            state.total = roundTo(state.total + diff, 2);

            return {...state}
        }
        case REMOVE_FRESHSALADS_CHOICE: {
            const {type, key} = action.payload;

            state[type].selected[key] = state[type].selected[key] - 1;
            // if (state[type].selected[key] === 0) {
            //     delete state[type].selected[key]
            // }

            // Subtotal
            const box_total = state[type].total;
            state[type].qty = state[type].qty - 1;
            state[type].total = roundTo(state[type].qty * state[type].price, 2);
            const diff = state[type].total - box_total;

            // Get Total
            state.total = roundTo(state.total + diff, 2);

            return {...state}
        }
        case CLOSE_FRESHSALADS_BOX: {
            state.box = null;
            state.mode = null;

            return {...state};
        }
        default :
            return state;
    }
}