import React from 'react';
import {
  BrowserRouter as Router,
  Route
} from 'react-router-dom'
import './App.css';
import { Container, Row, Col } from 'react-bootstrap';
import Banner from './img/Catalyst_Cafe_Catering_Banner.jpg';
// import Menu from './components/Menu';
import BagelShmear from './components/BagelShmear';
import EggSandwich from './components/EggSandwich';
import BreakfastFavorites from './components/BreakfastFavorites';
import SweetSnack from './components/SweetSnack';
import Beverage from './components/Beverage';
import FreshSalads from './components/FreshSalads';
import Cookie from './components/Cookie';
import LunchSandwich from './components/LunchSandwich';

import Summary from './components/Summary';
import Confirmation from './components/Confirmation';
// import Bottom from './components/bottom';

export default function App() {
  return (
    <Container className="su-catering-container" style={{marginTop: '1rem', marginBottom: '2rem'}}>

      <Row>
        <img src={Banner} style={{width: '100%'}} alt="Banner" />
      </Row>

      <Row style={{backgroundColor: '#fff'}}>
        <Col md={12} className="wrap-main">
          <Router basename={'/catering/online/einsteins'}>
            <Route exact path="/" component={BagelShmear} />
            <Route exact path="/bagelandshmear" component={BagelShmear} />
            <Route exact path="/eggsandwich" component={EggSandwich} />
            <Route exact path="/breakfastfavorites" component={BreakfastFavorites} />
            <Route exact path="/sweetsnack" component={SweetSnack} />
            <Route exact path="/beverage" component={Beverage} />
            <Route exact path="/freshsalads" component={FreshSalads} />
            <Route exact path="/cookies" component={Cookie} />
            <Route exact path="/lunchsandwich" component={LunchSandwich} />

            <Route exact path="/summary" component={Summary} />
            <Route exact path="/confirmation" component={Confirmation} />
          </Router>
        </Col>
      </Row>

    </Container>
  );
}
