import { 
    UPDATE_SWEETSNACK_QTY,
    OPEN_SWEETSNACK_BOX,
    OPEN_EDIT_SWEETSNACK_BOX,
    CLOSE_SWEETSNACK_BOX,
    ADD_SWEETSNACK_BOX,
    UPDATE_SWEETSNACK_BOX,
    REMOVE_SWEETSNACK_BOX
} from '../actions/actionTypes';

const initialState = {
    salad_large: {name: 'Seasonal Fresh Fruit Salad', price: 49.99, qty: 0, total: 0, description: 'An Assortment of Fresh-Cut Seasonal Fruit'},
    salad_small: {name: '', price: 29.99, qty: 0, total: 0, description: ''},
    parfait: {name: 'Vanilla Strawberry Granola Parfait - Group', price: 29.99, qty: 0, total: 0, description: 'Fresh-Cut Strawberries, Low-Fat Vanilla Yogurt & Premium Granola with Sliced Almonds & Craisins Dried Cranberries'},
    parfait_individual: {name: 'Vanilla Strawberry Granola Parfait - Individual', price: 3.79, qty: 0, total: 0, description: 'Fresh-Cut Strawberries, Low-Fat Vanilla Yogurt & Premium Granola with Sliced Almonds & Craisins Dried Cranberries'},
    box: null,
    type: null,
    mode: null,
    id: null,
    noshbox_half: {price: 18.99, qty: 0, total: 0},
    noshbox_dozen: {price: 32.49, qty: 0, total: 0},
    noshbox: {
        name: "Sweets Nosh Box",
        description: "Muffins & Assorted Pastries",
        items: {
            applewood: {name: 'Applewood Bacon & Cheddar'},
            turkey: {name: 'Turkey-Sausage & Cheddar'},
            ham: {name: 'Ham & Swiss'}
        },
        half: {
            price: 18.99,
            qty: 0,
            total: 0,
            list: [],
            selected: true
        },
        dozen: {
            price: 32.49,
            qty: 0,
            total: 0,
            list: [],
            selected: true
        },
        total: 0
    },
    total: 0
}

const roundTo = require('roundto');

export default function(state = initialState, action) {
    switch (action.type) {
        case UPDATE_SWEETSNACK_QTY : {
            const {name, qty} = action.payload;
            state[name].total = roundTo(state[name].price * qty, 2);
            state[name].qty = qty;
            state.total = roundTo(
                                state.salad_large.total + 
                                state.salad_small.total +
                                state.parfait.total +
                                state.parfait_individual.total +
                                state.noshbox_half.total +
                                state.noshbox_dozen.total
                                ,2);

            return {...state};
        }
        case OPEN_SWEETSNACK_BOX : {
            const {name, mode} = action.payload;
            const [box, type] = name.split('_');

            state.box = box;
            state.type = type;
            state.mode = mode;

            return {...state};
        }
        case OPEN_EDIT_SWEETSNACK_BOX : {
            const {box, type, id} = action.payload;

            state.box = box;
            state.type = type;
            state.id = id;
            state.mode = 'edit';

            return {...state}
        }
        case ADD_SWEETSNACK_BOX : {
            const {list} = action.payload;

            state[state.box][state.type].list.push(list);                                   
            state[state.box][state.type].qty = state[state.box][state.type].qty + 1;

            // Get Subtotal
            const box_total = state[state.box][state.type].total;
            state[state.box][state.type].total = roundTo(state[state.box][state.type].qty * state[state.box][state.type].price, 2);
            const diff = state[state.box][state.type].total - box_total;

            // Get Total
            state.total = state.total + diff;

            // Reset box, type, mode
            state.box = null;
            state.type = null;
            state.mode = null;

            return {...state}
        }
        case UPDATE_SWEETSNACK_BOX : {
            const {list} = action.payload;

            state[state.box][state.type].list[state.id] = list;

            // Reset box, type, mode
            state.box = null;
            state.type = null;
            state.mode = null;

            return {...state}
        }
        case REMOVE_SWEETSNACK_BOX: {
            const {box, type, id} = action.payload;

            state[box][type].list[id] = null;
            state[box][type].qty = state[box][type].qty - 1;

            // Subtotal
            const box_total = state[box][type].total;
            state[box][type].total = roundTo(state[box][type].qty * state[box][type].price, 2);
            const diff = state[box][type].total - box_total;

            // Get Total
            state.total = state.total + diff;

            return {...state}
        }
        case CLOSE_SWEETSNACK_BOX: {
            state.box = null;
            state.type = null;
            state.mode = null;

            return {...state};
        }
        default :
            return state;
    }
}