import { combineReducers } from "redux";
import main from "./main";
import bagelandsheamr from "./bagelandsheamr_reducer";
import egg_sandwich from "./egg_sandwiches_reducer";
import bk_fav_reducer from "./bk_fav_reducer";
import sweet_snack from "./sweet_snack_reducer";
import beverage from "./beverage";
import fresh_salads from "./fresh_salads_reducer";
import cookie from "./cookie_reducer";
import lunchsandwich from "./lunchsandwich_reducer";

export default combineReducers({ main, bagelandsheamr, egg_sandwich, bk_fav_reducer, sweet_snack, beverage, fresh_salads, cookie, lunchsandwich });